import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Events, Platform, AlertController, 
  ModalController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { AnnoncesPage } from '../annonces/annonces';
import { DentartPage } from '../dentart/dentart';
import { FormationsPage } from '../formations/formations';
import { ASavoirPage } from '../a-savoir/a-savoir';
import { UprofilPage } from '../uprofil/uprofil';
import { NativePageTransitions, NativeTransitionOptions } from '@ionic-native/native-page-transitions';
import { GeneralcndPage } from '../generalcnd/generalcnd';
import { MentlPage } from '../mentl/mentl';
import { LogoutpopupPage } from '../logoutpopup/logoutpopup';
import { ContactPage } from '../contact/contact';
import { File } from '@ionic-native/file';



/**
 * Generated class for the MenuPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-menu',
  templateUrl: 'menu.html',
})
export class MenuPage {

  pages: Array<{ title: string, component: any }>;

  user: any = {};

  canLeave: boolean = false;
  activeAcc: number = 0;

  menuClick: string = "";

  avatarPath = "file:///data/user/0/com.maa.dentart/files/profil.jpeg"
  avatar = null

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private viewCtrl: ViewController,
    private events: Events,
    private platform: Platform,
    private nativePageTransitions: NativePageTransitions,
    private alertCtrl: AlertController,
    private modalCtrl: ModalController,
    private file: File
  ) {

    file.readAsDataURL(file.dataDirectory, 'profil.jpeg')
    .then(
      data => {
        console.log("Avatar got")
        console.log(data.substr(0, 30))
        this.avatar = data
      },
      error => {
        console.log("Error loading avatar")
        console.log(JSON.stringify(error))
      }
    )

    this.user = navParams.get("user");
    console.log(this.user.avatar)
    console.log(this.user.photo)
    

    this.pages = [
      { title: 'Partages', component: HomePage },
      { title: 'Annonces', component: AnnoncesPage },
      { title: 'Formations', component: FormationsPage },
      { title: 'News', component: ASavoirPage },
      //{ title: 'Mes alertes', component: HomePage }
    ];

    platform.registerBackButtonAction(() => {
      this.viewCtrl.dismiss({ title: '', component: null });
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MenuPage');
  }

  ionViewCanLeave(): boolean {
    return true;
  }

  ionViewWillLeave() {
    //
  }

  public ionViewWillEnter(): void {
    this.navCtrl.swipeBackEnabled = true;
  }

  public ionViewDidLeave(): void {
    this.navCtrl.swipeBackEnabled = false;
  }

  onDidDismiss() {
    return true;
  }

  openPage(page, menu) {
    this.menuClick = menu;
    setTimeout(() => {
      this.viewCtrl.dismiss(page);
    }, 200);

  }

  public logOut() {

    let cfrm = this.modalCtrl.create(LogoutpopupPage, {});
    cfrm.onDidDismiss(data => {
      if (data.state == 'oui') {
        this.viewCtrl.dismiss({ title: 'deconnexion', component: null });
      }
      else {
        // this.viewCtrl.dismiss({ title: '', component: null });
      }
    })
    cfrm.present()

  }

  public closeMenu() {
    let boxOne = document.getElementsByClassName('ext')[0];

    boxOne.classList.add('horizTranslate');

    setTimeout(() => {
      this.viewCtrl.dismiss({ title: '', component: null },
        '',
        {
          animate: true,
        });
    }, 0);


  }

  swipeEvent(e) {
    if (e.direction == 2)
      this.closeMenu();
  }

  public viewProfil() {
    this.viewCtrl.dismiss({ title: 'profil', component: UprofilPage });
  }

  public showMoncompte() {

    if (this.activeAcc == 0) {
      document.getElementById('my_account').style.display = 'block';
      this.activeAcc = 1;
      this.menuClick = 'acc';
    }
    else {
      document.getElementById('my_account').style.display = 'none';
      this.activeAcc = 0;
      this.menuClick = '';
    }

  }

  public openAlert() {
    this.viewCtrl.dismiss({ title: 'Mes alertes', component: HomePage });
  }

  public openAds() {
    this.viewCtrl.dismiss({ title: 'mesannonces', component: HomePage });
  }

  public openContact() {

    let contactModal = this.modalCtrl.create(ContactPage,{
      user: this.user
    })
    contactModal.onDidDismiss(data => {
      console.log("Contact action")
      console.log(JSON.stringify(data))
    })
    contactModal.present()
    
  }

  public showCGU() {
    let cgu = this.modalCtrl.create(GeneralcndPage,
      {
        source: 'menu'
      });

    cgu.onDidDismiss(data => {
      this.platform.registerBackButtonAction(() => {
        this.viewCtrl.dismiss({ title: '', component: null });
      });
      console.log("CGU");
    });

    cgu.present();
  }

  public showML() {
    let cgu = this.modalCtrl.create(MentlPage,
      {
        source: 'menu'
      });

    cgu.onDidDismiss(data => {
      this.platform.registerBackButtonAction(() => {
        this.viewCtrl.dismiss({ title: '', component: null });
      });
      console.log("CGU");
    });

    cgu.present();
  }

}



