import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, AlertController, Platform } from 'ionic-angular';
import { HomePage } from '../home/home';
import { HTTP } from '@ionic-native/http';
import { GlobalsProvider } from '../../providers/globals/globals';
import { BrowserTab } from '@ionic-native/browser-tab';
import { Toast } from '@ionic-native/toast';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the GeneralcndPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-generalcnd',
  templateUrl: 'generalcnd.html',
})
export class GeneralcndPage {

  pseudo: string = '';
  job: string = '';
  address: string = '';
  mail: string = '';
  password: string = '';

  source: string = "";
  load: number = 0;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private http: HTTP,
    private globals: GlobalsProvider,
    private viewCtrl: ViewController,
    private alertCtrl: AlertController,
    private browserTab: BrowserTab,
    private toast: Toast,
    private storage: Storage,
    private platform: Platform
  ) {

    this.job = navParams.get('job');
    this.pseudo = navParams.get('pseudo');
    this.address = navParams.get('address');
    this.mail = navParams.get('mail');
    this.password = navParams.get('password');

    this.source = navParams.get('source');

    platform.registerBackButtonAction(() => {
      this.viewCtrl.dismiss({
        state: "oui"
      });
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad GeneralcndPage');
  }

  public precedent() {
    if (this.source == 'gcdn' || this.source == 'menu') {
      this.viewCtrl.dismiss({
        state: "non"
      });
    }
    else
      this.navCtrl.pop();
  }

  public goToHome() {
    let home = HomePage;
    this.navCtrl.push(home);
  }

  public signUp() {
    if (this.source == 'gcdn' || this.source == 'menu') {
      this.viewCtrl.dismiss({
        state: "oui"
      });
    }
    else {
      this.load = 1;
      console.log("Full address")
      console.log(this.address)
      this.http.post(this.globals.variables.urls.createAccountURL,
        {
          "mail": this.mail,
          "pseudo": this.pseudo,
          "job": this.job,
          "address": JSON.parse(this.address).description,
          "password": this.password,
          "full_address": this.address
        }, {})
        .then(data => {
          this.load = 0;
          let res = JSON.parse(data.data);
          console.log(res);
          console.log(res.etat_inscription);
          if (res.etat_inscription == 'inscription-ok') {
            this.connect()
            // let newUser = res.user_data
            // console.log("Parsing")
            // let full_address = JSON.parse(newUser.full_address)
            // console.log("Parsed")
            
            // newUser.id_User += ""
            // newUser.user_email = newUser.mail
            // // newUser.full_address = JSON.parse(this.address)
            
            // this.saveSession(newUser)
            // console.log("Retour user after signup :")
            // console.log(JSON.stringify(newUser))
            // let home = HomePage;
            // this.navCtrl.setRoot(home, {
            //   user: newUser,
            //   animate: true,
            //   direction: 'forward'
            // });
          }
          else if (res.etat_inscription == 'inscription-invalid') {
            let alert = this.alertCtrl.create({
              
              title: 'Information',
              subTitle: res.error_name,
              buttons: ['Ok']
            });
            alert.present();
          }
          console.log(JSON.stringify(data.data));
        }, error => {
          this.load = 0;
          let alert = this.alertCtrl.create({
            title: 'Information',
            subTitle: 'Erreur de connexion avec le serveur.',
            buttons: ['Ok']
          });
          alert.present();
          console.log(JSON.stringify(error));
        });
    }

  }

  public saveSession(user) {
    this.storage.set('user', user)
      .then(data => {
        // console.log(JSON.stringify(data));
        // let home = HomePage;
        // this.navCtrl.setRoot(home, {
        //   user: user
        // },
        //   {
        //     animate: true,
        //     animation: 'ios-transition',
        //     direction: 'forward',
        //     duration: 500
        //   });
      }, 
      error => {
        console.log(JSON.stringify(error));
      });

  }

  public connect() {

    // this.isBtnConnect = true;

    if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.mail))) {
      console.log("Mail invalid.");
      // this.isBtnConnect = false;
      let alert = this.alertCtrl.create({
        title: 'Information',
        subTitle: 'Votre e-mail est invalide.',
        buttons: ['Ok']
      });
      alert.present();
      // this.isBtnConnect = false;
    }
    else if (this.password.length < 8) {
      // this.isBtnConnect = false;
      console.log("Password invalid.");
      let alert = this.alertCtrl.create({
        title: 'Information',
        subTitle: 'Votre mot de passe doit contenir au minimum 8 caractères.',
        buttons: ['Ok']
      });
      alert.present();
      // this.isBtnConnect = false;
    }
    if (/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.mail) &&
      this.password.length >= 8) {

      this.http.post(this.globals.variables.urls.loginURL,
        {
          "mail": this.mail,
          "password": this.password
        }, {})
        .then(data => {
          let res = JSON.parse(data.data);
          //console.log(res);
          //console.log(res.etat_inscription);

          if (res.etat_connexion == 'connexion-ok') {
            let user = res.info_utilisateur;
            // this.events.publish("user:logedIn", this.user);
            this.saveSession(user);
            let home = HomePage;
            this.navCtrl.setRoot(home, {
              user: user
            },
              {
                animate: true,
                animation: 'ios-transition',
                direction: 'forward',
                duration: 500
              });
          }
          else if (res.etat_connexion == 'non-ok') {
            // this.isBtnConnect = false;
            let alert = this.alertCtrl.create({
              title: 'Information',
              subTitle: 'Votre email ou mot de passe est incorrect.',
              buttons: ['Ok']
            });
            alert.present();
          }
          //console.log(JSON.stringify(data.data));
        }, error => {
          // this.isBtnConnect = false;
          console.log(JSON.stringify(error));
          let alert = this.alertCtrl.create({
            title: 'Information',
            subTitle: 'Erreur d\'accès au serveur.',
            buttons: ['Ok']
          });
          alert.present();
        });

    }

  }

  public openUrl(url) {
    this.browserTab.isAvailable()
        .then(isAvailable => {
          if (isAvailable) {
            this.browserTab.openUrl(url);
          } else {
            this.toast.show("Navigateur non disponible", "2000", "bottom").subscribe(
              toast => {
                console.log(toast);
              }
            );
          }
        });
  }

}



