import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ToastController } from 'ionic-angular';
import { ConnexionPage } from '../connexion/connexion';
import { HTTP } from '@ionic-native/http';
import { GlobalsProvider } from '../../providers/globals/globals';

/**
 * Generated class for the RecoverpasswdPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-recoverpasswd',
  templateUrl: 'recoverpasswd.html',
})
export class RecoverpasswdPage {

  mail: string = "";

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private alertCtrl: AlertController,
    private http: HTTP,
    private toastCtrl: ToastController,
    private globals: GlobalsProvider
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RecoverpasswdPage');
  }

  public retour() {
    this.navCtrl.pop();
  }

  public recover() {
    if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.mail))) {
      console.log("Mail invalid.");
      let alert = this.alertCtrl.create({
        title: 'Information',
        subTitle: 'Votre e-mail est invalide.',
        buttons: ['Ok']
      });
      alert.present();
    }
    else {
      //lostPasswd
      this.http.post(this.globals.variables.urls.lostPasswd + "?mail=" + this.mail, 
        {
          "mail": this.mail
        }, {})
        .then(data => {
          let res = JSON.parse(data.data);
          console.log(JSON.stringify(data.data))
          if (res.status == 'ok') {
            this.toastCtrl.create(
              {
                message: "Un mail vous a été envoyé",
                position: "bottom",
                duration: 5000
              }
            ).present();
          }
          else {
            this.toastCtrl.create(
              {
                message: "Verifier bien que votre adresse mail est correcte !",
                position: "bottom",
                duration: 5000
              }
            ).present();
          }
          this.navCtrl.pop();
        },
          error => {
            this.toastCtrl.create(
              {
                message: "Erreur de connexion au serveur",
                position: "bottom",
                duration: 5000
              }
            ).present();
            this.navCtrl.pop();
          });

    }

  }

  public emailTyping() {
    if (/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.mail)) {
      console.log("Mail valid.");
    }
    else {
      console.log("Mail invalid.");
    }
  }

}



