  public showGalery() {
    this.photoLibrary.requestAuthorization().then(() => {
      this.photoLibrary.getLibrary().subscribe({
        next: library => {
          this.phoneLibPhotos = library;
          //this.listImage = library;
          for (let i = 0; i < 12; i++) {
            let url = 'file://' + this.phoneLibPhotos[i].id.substring(this.phoneLibPhotos[i].id.lastIndexOf(';') + 1, this.phoneLibPhotos[i].id.length);
            let dirEntry = url.substring(0, url.lastIndexOf('/'));
            let fileName = url.substring(url.lastIndexOf('/') + 1, url.length);

            this.file.readAsDataURL(dirEntry, fileName)
              .then(urlBase64 => {
                let img = {
                  id: this.phoneLibPhotos[i].id,
                  url: urlBase64,
                  nativeURL: url,
                  data: {
                    nativeURL: url
                  }
                };
                this.listImage.push(img);
                if (this.selectedImg == null) this.selectedImg = this.listImage[0];
                if (i == 11) {
                  this.range += 1;
                  this.load = 0;
                }
                //console.log(url);
              })
              .catch(error => {
                this.load = 0;
                console.log("Can not read file");
                console.log(JSON.stringify(error));
              });
          }

        },
        error: err => { console.log('could not get photos'); },
        complete: () => { console.log('done getting photos'); }
      });
    })
      .catch(err => console.log('permissions weren\'t granted'));
  }

  public doInfinite(infiniteScroll) {
    if (this.range * 12 >= this.phoneLibPhotos.length) {
      infiniteScroll.complete();
      return;
    }
    let limit = this.range * 24 < this.phoneLibPhotos.length ? this.range * 24 : this.phoneLibPhotos.length;
    for (let i = this.range * 12; i < limit; i++) {
      let url = 'file://' + this.phoneLibPhotos[i].id.substring(this.phoneLibPhotos[i].id.lastIndexOf(';') + 1, this.phoneLibPhotos[i].id.length);
      let dirEntry = url.substring(0, url.lastIndexOf('/'));
      let fileName = url.substring(url.lastIndexOf('/') + 1, url.length);
      console.log("=========================================");
      console.log(dirEntry);
      console.log(fileName);

      this.file.readAsDataURL(dirEntry, fileName)
        .then(urlBase64 => {
          let img = {
            id: this.phoneLibPhotos[i].id,
            url: urlBase64,
            nativeURL: url,
            data: {
              nativeURL: url
            }
          };
          this.listImage.push(img);
          if (this.selectedImg == null) this.selectedImg = this.listImage[0];
          if (i == limit - 1) {
            this.range += 1;
            infiniteScroll.complete();
          }
        })
        .catch(error => {
          console.log(JSON.stringify(error));
        });

    }
  }

