import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Platform } from 'ionic-angular';
import { AnnoncesPage } from '../annonces/annonces';

/**
 * Generated class for the AnnonceAddAnnulerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-annonce-add-annuler',
  templateUrl: 'annonce-add-annuler.html',
})
export class AnnonceAddAnnulerPage {

  user: any = {};
  source: string = "";

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private viewCtrl: ViewController,
    private platform: Platform
  ) {

    this.user = navParams.get('user');
    this.source = navParams.get('source');

    this.platform.registerBackButtonAction(() => {
      this.dismiss();
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AnnonceAddAnnulerPage');
  }

  public dismiss(){
    this.viewCtrl.dismiss({etat: "non"});
  }

  public annuler(){
    if (this.source == "update") {
      this.viewCtrl.dismiss({etat: "update"});
    }
    else {
      // let home = AnnoncesPage;
      // this.navCtrl.setRoot(home, 
      //   {user: this.user}, 
      //   {animate: true});
      this.viewCtrl.dismiss({etat: "annonces"});
    }
    
  }

  public publier(){
    let home = AnnoncesPage;
    this.navCtrl.setRoot(home,
      {user: this.user},  
      {animate: true});
  }

}



