import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events,
  Platform, AlertController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { Createacc1Page } from '../createacc1/createacc1';
import { ConnexionPage } from '../connexion/connexion';
import { File } from '@ionic-native/file';
import { HomePage } from '../home/home';
import { Network } from '@ionic-native/network';
import { NonetworkPage } from '../nonetwork/nonetwork';
import { Storage } from '@ionic/storage';
import { GlobalsProvider } from '../../providers/globals/globals';
import { HTTP } from '@ionic-native/http';
import { AndroidPermissions } from '@ionic-native/android-permissions';
import { Sim } from '@ionic-native/sim';
import { LocationAccuracy } from '@ionic-native/location-accuracy';

declare var GPSLocation;

@IonicPage()
@Component({
  selector: 'page-dentart',
  templateUrl: 'dentart.html',
})
export class DentartPage {

  langage: string = 'fr';
  user: any = {};
  locationCoords: any;
  timetest: any;
  intervalChecklocation: any = 0;
  iCl: any = 0;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private events: Events,
    private file: File,
    private network: Network,
    private storage: Storage,
    private globals: GlobalsProvider,
    private http: HTTP,
    private androidPermissions: AndroidPermissions,
    private platform: Platform,
    private sim: Sim,
    private alertCtrl: AlertController,
    private locationAccuracy: LocationAccuracy,
    private geolocation: Geolocation
  ) {
    this.locationCoords = {
      latitude: "",
      longitude: "",
      accuracy: "",
      timestamp: ""
    }
    this.timetest = Date.now();
    //this.testConnectivity();

  }

  ionViewWillLoad() {
    console.log('ionViewWillLoad DentartPage');
    // this.getSession();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DentartPage');
    // this.testConnectedUser();
    this.checkReadWritePermissions()
    this.getCertText()
  }

  getCertText() {
    this.platform.ready().then(
      ready => {
        this.http.get(
          this.globals.variables.urls.getCertText,
          {},
          {}
        )
          .then(
            data => {
              console.log("Certificate text :")
              console.log(JSON.stringify(data.data))
              // this.storage.set("cert_text", data.data)
              this.storage.set("cert_text", JSON.parse(data.data).text_after_cnx)
                .then(
                  data => {
                    console.log("Cert text saved to storage : ")
                    console.log(JSON.stringify(data))
                  },
                  error => {
                    console.log("Error set cert text to storage : ")
                    console.log(JSON.stringify(error))
                  }
                )
            },
            error => {
              console.log("Error getting certText : ")
              console.log(JSON.stringify(error))
              setTimeout(
                () => {
                  this.getCertText()
                },
                5000
              )
            }
          )
      },
      error => {
        console.log("Platform error")
      }
    )

  }

  public checkReadWritePermissions() {

    this.locationAccuracy.request(this.locationAccuracy.REQUEST_PRIORITY_HIGH_ACCURACY).then(
      () => {
      this.androidPermissions.checkPermission(this.androidPermissions.PERMISSION.ACCESS_COARSE_LOCATION).then(
        result => {
          if (result.hasPermission) {
            console.log('********************askToTurnOnGPS*****************')
            this.askToTurnOnGPS();
            this.intervalChecklocation = 1;
          } else {
            this.iCl = setInterval(() => {
              if(this.intervalChecklocation == 0) {
                this.getLocationCoordinates();
              } else {
                clearInterval(this.iCl);
              }
            },500);
          }
        },
        err => {
          alert(err);
        }
      );
      },
      error => console.log('Error requesting location permissions', error)
    );
      
      this.androidPermissions.requestPermissions([this.androidPermissions.PERMISSION.FOREGROUND_SERVICE,this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE,this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE,this.androidPermissions.PERMISSION.CAMERA, this.androidPermissions.PERMISSION.GET_ACCOUNTS]);

      this.sim.requestReadPermission().then(
        () => console.log('Permission granted'),
        () => console.log('Permission denied')
      );
  }

  public askToTurnOnGPS() {
    this.locationAccuracy.request(this.locationAccuracy.REQUEST_PRIORITY_HIGH_ACCURACY).then(
      () => {
        // When GPS Turned ON call method to get Accurate location coordinates
        this.getLocationCoordinates()
      },
      error => console.log('Error requesting location permissions ' + JSON.stringify(error))
    );
  }

  public getLocationCoordinates() {
    this.geolocation.getCurrentPosition().then((resp) => {
      this.locationCoords.latitude = resp.coords.latitude;
      this.locationCoords.longitude = resp.coords.longitude;
      this.locationCoords.accuracy = resp.coords.accuracy;
      this.locationCoords.timestamp = resp.timestamp;
      console.log("*************LOCATIONCORDS******************")
      console.log(JSON.stringify(this.locationCoords))
      this.intervalChecklocation = 1;
    }).catch((error) => {
      console.log("Erreur permission geolocation", JSON.stringify(error))
    });
    

    let watch = this.geolocation.watchPosition();
    watch.subscribe((data) => {
    // console.log("Watching Position");
    //   console.log(JSON.stringify(data));
    });
  }

  public creerCompte() {
    let cacc = Createacc1Page;
    this.navCtrl.push(cacc);
  }

  public connexion() {
    let conn = ConnexionPage;
    this.navCtrl.push(conn);
  }

  public updateSessionInfos() {
    let user_id = this.user.id_User != null ? this.user.id_User : this.user.id
    this.http.get(this.globals.variables.urls.getUserInfo + "?user_id=" + user_id,
      {},
      {})
      .then(
        (userInfo) => {
          console.log("%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%%")
          console.log(JSON.stringify(JSON.parse(userInfo.data)))
          let newUserInfo = JSON.parse(userInfo.data).info_utilisateur
          newUserInfo.avatarB64 = this.user.avatarB64
          this.storage.set('user', newUserInfo)
          let home = HomePage;
          this.navCtrl.setRoot(home, {
            user: newUserInfo
          },
            {
              animate: true,
              animation: 'ios-transition',
              direction: 'forward',
              duration: 500
            });
        }
      )
  }

  public getSession() {
    this.storage.get('user')
      .then(
        data => {
          console.log(JSON.stringify(data));
          this.user = data;
          if (this.user != null) {
            this.events.publish('user:logedIn', this.user);

            this.updateSessionInfos()
          }
        },
        error => {
          console.log(JSON.stringify(error));
        }
      )
  }

  public testConnectedUser() {

    this.file.checkDir(this.file.dataDirectory, 'user/')
      .then(
        data => {
          console.log("User directory found.");
          console.log(JSON.stringify(data));
          this.file.checkFile(this.file.dataDirectory + "user/", "user.json")
            .then(
              data => {
                console.log("File user exist");
                this.file.readAsText(this.file.dataDirectory + "user/", 'user.json')
                  .then(
                    data => {
                      // JSON.stringify(data);
                      this.user = JSON.parse(data);
                      this.events.publish('user:logedIn', this.user);
                      let home = HomePage;
                      this.navCtrl.setRoot(home, {
                        user: this.user
                      },
                        {
                          animate: true,
                          animation: 'ios-transition',
                          direction: 'forward',
                          duration: 500
                        });
                    },
                    error => {
                      console.log("Can not read user file as text");
                      console.log(JSON.stringify(error));
                    }
                  );
              },
              error => {
                console.log("File user does not exist");
                console.log(JSON.stringify(error));
              }
            );
        },
        error => {
          console.log("User directory not found.");
          console.log(JSON.stringify(error));
          this.file.createDir(this.file.dataDirectory, "user/", true)
            .then(
              data => {
                console.log("User directory creadted");
              },
              error => {
                console.log("Can not create user directory");
                console.log(JSON.stringify(error));
              }
            );
        }
      );


  }

  public testConnectivity() {
    console.log("Testing network");
    if (this.network.type == 'none' || this.network.type == 'unknown') {
      this.navCtrl.setRoot(NonetworkPage);
    }
    else {
      //this.navCtrl.setRoot(DentartPage)
    }
    setTimeout(() => {
      this.testConnectivity();
    }, 5000);
  }

}



