import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Platform } from 'ionic-angular';
import { GlobalsProvider } from '../../providers/globals/globals';
import { HTTP } from '@ionic-native/http';

/**
 * Generated class for the Faqadd2Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-faqadd2',
  templateUrl: 'faqadd2.html',
})
export class Faqadd2Page {

  user: any = {};

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private viewCtrl: ViewController,
    private globals: GlobalsProvider,
    private http: HTTP,
    private platform: Platform
  ) {

    this.user = navParams.get('user');

    platform.registerBackButtonAction(() => {
      viewCtrl.dismiss()
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad Faqadd2Page');
  }

  public annuler() {
    this.viewCtrl.dismiss(null);
  }

  public dismiss() {
    this.changeRPCState();
    let data = {
      user: 1,
      clicked: 'ok'
    }
    this.viewCtrl.dismiss(data);
  }

  public downloadRPC() {
    this.viewCtrl.dismiss(null);
  }

  public changeRPCState() {
    let id_user = this.user.id_User != null ? this.user.id_User : this.user.id;
    this.http.get(this.globals.variables.urls.changeRPC + "?id_user=" + id_user + "&status=1",
      {},
      {})
      .then(
        data => {
          this.user.understand = '1';
          console.log(JSON.stringify(data.data));
        },
        error => {
          console.log(JSON.stringify(error));
        }
      )
  }

}



