import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Platform } from 'ionic-angular';
import { MailtoPage } from '../mailto/mailto';
import { CallNumber } from '@ionic-native/call-number';
import { GlobalsProvider } from '../../providers/globals/globals';
import { HTTP } from '@ionic-native/http';
import { Toast } from '@ionic-native/toast';
import { ImageviewerPage } from '../imageviewer/imageviewer';
import { AnnonceAdd2Page } from '../annonce-add2/annonce-add2';

/**
 * Generated class for the AnnonceDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-annonce-details',
  templateUrl: 'annonce-details.html',
})
export class AnnonceDetailsPage {

  @ViewChild('mySlider') mySlider;

  user: any;
  ad: any;
  nb_img_ad: Array<any> = [];
  active_image: any = 1;
  contactAuthor: boolean = false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private modalCtrl: ModalController,
    private callNumber: CallNumber,
    private globals: GlobalsProvider,
    private http: HTTP,
    private toast: Toast,
    private platform: Platform
  ) {

    this.user = navParams.get('user');
    this.ad = navParams.get('ad');
    this.ad.images_count = 0;
    console.log(JSON.stringify(this.user));
    console.log(JSON.stringify(this.ad));
    // faq_image_1
    if (this.ad.ad_image_1 != null && this.ad.ad_image_1 != '') {
      this.nb_img_ad.push('1');
      this.ad.images_count += 1;
    }
    if (this.ad.ad_image_2 != null && this.ad.ad_image_2 != '') {
      this.nb_img_ad.push('2');
      this.ad.images_count += 1;
    }
    if (this.ad.ad_image_3 != null && this.ad.ad_image_3 != '') {
      this.nb_img_ad.push('3');
      this.ad.images_count += 1;
    }
    if (this.ad.ad_image_4 != null && this.ad.ad_image_4 != '') {
      this.nb_img_ad.push('4');
      this.ad.images_count += 1;
    }

    ///
    if (this.ad.faq_image_1 != null && this.ad.faq_image_1 != '') {
      this.nb_img_ad.push('1');
      this.ad.images_count += 1;
    }
    if (this.ad.faq_image_2 != null && this.ad.faq_image_2 != '') {
      this.nb_img_ad.push('2');
      this.ad.images_count += 1;
    }
    if (this.ad.faq_image_3 != null && this.ad.faq_image_3 != '') {
      this.nb_img_ad.push('3');
      this.ad.images_count += 1;
    }
    if (this.ad.faq_image_4 != null && this.ad.faq_image_4 != '') {
      this.nb_img_ad.push('4');
      this.ad.images_count += 1;
    }

    this.active_image = parseInt(this.nb_img_ad[0]);

    platform.registerBackButtonAction(() => {
      navCtrl.pop();
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AnnonceDetailsPage');
  }

  public slideChanged() {
    console.log(this.mySlider.getActiveIndex());
    if (this.mySlider.getActiveIndex() < this.nb_img_ad.length) {
      this.active_image = parseInt(this.nb_img_ad[this.mySlider.getActiveIndex()]);
    }

  }

  public precedent() {
    this.navCtrl.pop();
  }

  public showMailTo() {
    let mailto = this.modalCtrl.create(MailtoPage, {
      user: this.user,
      ad: this.ad,
      mail: this.ad.author_infos.user_email,
      post_id: this.ad.id,
      object: this.ad.title
    });
    mailto.onDidDismiss(data => {
      this.platform.registerBackButtonAction(() => {
        this.navCtrl.pop();
      });
      console.log(JSON.stringify(data));
    });
    mailto.present();
  }

  public callAuthor() {
    if (this.ad.phone != '') {
      this.callNumber.callNumber(this.ad.phone, true)
        .then(res => console.log('Launched dialer!', res))
        .catch(err => console.log('Error launching dialer', err));
    }
    else {
      this.toast.show("Pas de téléphone pour cette annonce !", "3000", "bottom")
        .subscribe(t => {
          console.log(JSON.stringify(t));
        })
    }

  }

  public report() {
    //
    //repportFaQ
    this.http.post(this.globals.variables.urls.repportFaQ, {
      "user_id": this.user.id_User != null ? this.user.id_User : this.user.id,
      "post_id": this.ad.id
    },
      {})
      .then(
        data => {
          let res = JSON.parse(data.data);
          console.log(JSON.stringify(res));
          if (res.status == "ok") {
            if (this.ad.user_report == 0) {
              this.ad.user_report = 1;
              this.toast.show("Vous avez signalé cette annonce", "2000", "bottom").subscribe(
                toast => {
                  console.log(toast);
                }
              );
            }
            else {
              this.ad.user_report = 0;
              this.toast.show("Votre signalisation a été annulée", "2000", "bottom").subscribe(
                toast => {
                  console.log(toast);
                }
              );
            }

          }
          else {
            console.log(JSON.stringify(res));
          }
          //console.log(JSON.stringify(data.data));
        },
        error => {
          console.log(JSON.stringify(error));
        }
      );
  }

  public viewImage(img) {
    let iv = this.modalCtrl.create(ImageviewerPage,
      {
        img: img
      });
    iv.onDidDismiss(() => {
      this.platform.registerBackButtonAction(() => {
        this.navCtrl.pop();
      });
    })
    iv.present();
  }

  
}



