  public onLocateUser() {
    this.intPos = setInterval(() => {
      this.geolocation.getCurrentPosition()
      .then(
        (location) => {
          console.log('position gotten: long:',location.coords.longitude,' lat:',location.coords.latitude);
          this.checklat = location.coords.latitude;
          this.location = location;
          this.gmLocation.lat = location.coords.latitude;
          this.gmLocation.lng = location.coords.longitude;
          if(this.checklat !== null) {
          let pos = this.gmLocation.lat + ',' + this.gmLocation.lng;
          this.http.get('https://maps.googleapis.com/maps/api/place/autocomplete/json?input=a&key=' + this.globals.variables.API_KEY + '&location=' + pos + '&radius=100',
            {},
            {})
            .then(
              data => {
                console.log("**************Result from autocomplete**************");
                // console.log(JSON.stringify(data.data));
                let predicts = JSON.parse(data.data).predictions;
                this.sideAddress = predicts[0];
              },
              error => {
                console.log("Error from autocomplete");
                console.log(JSON.stringify(error));
              }
            )
            clearInterval(this.intPos);
          }
        }
      )
      .catch(
        (error) => {
          console.log('Error getting location', error);
        }
      )
    },3000);
  }

  public setSideAddress() {
    if (this.sideAddress.description != null) {
      this.full_ad_address = JSON.stringify(this.sideAddress);
      this.place_id = this.sideAddress.place_id;
      this.ad_address = this.sideAddress.description;
      this.autocomplete.input = this.sideAddress.description;
      this.autocompleteItems = [];
      clearInterval(this.intPosClick);
    } else {
      this.toast.show("En attente du GPS", "5000", "bottom")
        .subscribe(
          t => {
            console.log(JSON.stringify(t));
          }
        );
      this.intPosClick = setInterval(() => {
        if (this.sideAddress.description != null) {
          this.full_ad_address = JSON.stringify(this.sideAddress);
          this.place_id = this.sideAddress.place_id;
          this.ad_address = this.sideAddress.description;
          this.autocomplete.input = this.sideAddress.description;
          this.autocompleteItems = [];
          clearInterval(this.intPosClick);
        } else {
          this.toast.show("En attente du GPS", "5000", "bottom")
        .subscribe(
          t => {
            console.log(JSON.stringify(t));
          }
        );
        }
      },5000)
    }
  }

  public requestAutoComplete() {

    GPSLocation.getCurrentPosition((position) => {
      console.log("Resultat du GPS");
      console.log(JSON.stringify(position));
      let pos = parseFloat(parseFloat(position.coords.latitude).toFixed(6)) +
        ',' + parseFloat(parseFloat(position.coords.longitude).toFixed(6));
      console.log("Position : " + pos);
      //https://maps.googleapis.com/maps/api/place/autocomplete/output?parameters
      this.http.get('https://maps.googleapis.com/maps/api/place/autocomplete/json?input=a&key=' + this.globals.variables.API_KEY + '&location=' + pos + '&radius=100',
        {},
        {})
        .then(
          data => {
            console.log("Result from autocomplete");
            console.log(JSON.stringify(data.data));
            let predicts = JSON.parse(data.data).predictions;
            this.sideAddress = predicts[0];
          },
          error => {
            console.log("Error from autocomplete");
            console.log(JSON.stringify(error));
          }
        )
    },
      (error) => {
        console.log("Erroeur du GPS");
        console.log(JSON.stringify(error));
        let alert = this.alertCtrl.create({
          title: 'Information',
          subTitle: 'Activer votre GPS, SVP.',
          buttons: ['Ok']
        });
        alert.present();
      });

  }

