import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Platform, ModalController, ViewController } from 'ionic-angular';
import { BrowserTab } from '@ionic-native/browser-tab';
import { Toast } from '@ionic-native/toast';
import { ImageviewerPage } from '../imageviewer/imageviewer';

/**
 * Generated class for the ASavoirDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-a-savoir-details',
  templateUrl: 'a-savoir-details.html',
})
export class ASavoirDetailsPage {

  @ViewChild('mySlider') mySlider;

  user: any = {};
  asv: any = {};
  nb_img_asv: any = [];
  active_image: any = 1;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private browserTab: BrowserTab,
    private toast: Toast,
    private platform: Platform,
    private modalCtrl: ModalController,
    private viewCtrl: ViewController
  ) {

    this.user = navParams.get('user');
    this.asv = navParams.get('asv');

    // faq_image_1
    if (this.asv.image_1 != null && this.asv.image_1 != '') this.nb_img_asv.push('1');
    if (this.asv.image_2 != null && this.asv.image_2 != '') this.nb_img_asv.push('2');
    if (this.asv.image_3 != null && this.asv.image_3 != '') this.nb_img_asv.push('3');
    if (this.asv.image_4 != null && this.asv.image_4 != '') this.nb_img_asv.push('4');

    //
    if (this.asv.faq_image_1 != null && this.asv.faq_image_1 != '') this.nb_img_asv.push('1');
    if (this.asv.faq_image_2 != null && this.asv.faq_image_2 != '') this.nb_img_asv.push('2');
    if (this.asv.faq_image_3 != null && this.asv.faq_image_3 != '') this.nb_img_asv.push('3');
    if (this.asv.faq_image_4 != null && this.asv.faq_image_4 != '') this.nb_img_asv.push('4');

    this.active_image = parseInt(this.nb_img_asv[0]);

    platform.registerBackButtonAction(() => {
      navCtrl.pop();
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ASavoirDetailsPage');
  }

  public slideChanged() {
    console.log(this.mySlider.getActiveIndex());
    if (this.mySlider.getActiveIndex() < this.nb_img_asv.length) {
      this.active_image = parseInt(this.nb_img_asv[this.mySlider.getActiveIndex()]);
    }

  }

  public retour() {
    this.navCtrl.pop();
  }

  public showURL() {

    if (this.asv.partner_url != '') {
      this.browserTab.isAvailable()
        .then(isAvailable => {
          if (isAvailable) {
            this.browserTab.openUrl(this.asv.partner_url);
          } else {
            this.toast.show("Navigateur non disponible", "2000", "bottom").subscribe(
              toast => {
                console.log(toast);
              }
            );
          }
        });
    }
    else {
      this.toast.show("Pas d'URL pour cette News", "2000", "bottom").subscribe(
        toast => {
          console.log(toast);
        }
      );
    }
    //partner_url

  }

  public viewImage(img) {
    let iv = this.modalCtrl.create(ImageviewerPage,
      {
        img: img
      });
    iv.onDidDismiss(() => {
      this.platform.registerBackButtonAction(() => {
        this.navCtrl.pop();
      });
    })
    iv.present();
  }

}



