import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events,
  Platform } from 'ionic-angular';
import { HomePage } from '../home/home';
import { Storage } from '@ionic/storage';
import { DentartPage } from '../dentart/dentart';
import { HTTP } from '@ionic-native/http';
import { GlobalsProvider } from '../../providers/globals/globals';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { BackgroundMode } from '@ionic-native/background-mode';
import { Push, PushObject, PushOptions } from '@ionic-native/push';

@IonicPage()
@Component({
  selector: 'page-authloading',
  templateUrl: 'authloading.html',
})
export class AuthloadingPage {

  user: any = null

  load = 0;
  // user: any = {}
  listForms: Array<any> = [];
  listNewNotifs: Array<any> = []
  listOldNotifs: Array<any> = []
  notifOffset: number = 0
  notifNewPostOffset: number = 0

  getNotifInterval: any = null

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private storage: Storage,
    private events: Events,
    private http: HTTP,
    private globals: GlobalsProvider,
    private localNotifications: LocalNotifications,
    private backgroundMode: BackgroundMode,
    private push: Push,
    private platform: Platform
  ) {


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AuthloadingPage');
    this.getSession();
    this.getCertText()
  }

  getCertText() {
    this.platform.ready().then(
      ready => {
        this.http.get(
          this.globals.variables.urls.getCertText,
          {},
          {}
        )
          .then(
            data => {
              console.log("Certificate text :")
              // console.log(JSON.stringify(data.data))
              console.log(data.data)
              console.log(JSON.parse(data.data).text_after_cnx)
              this.storage.set("cert_text", JSON.parse(data.data).text_after_cnx)
                .then(
                  data => {
                    console.log("Cert text saved to storage : ")
                    console.log(JSON.stringify(data))
                  },
                  error => {
                    console.log("Error set cert text to storage : ")
                    console.log(JSON.stringify(error))
                  }
                )
            },
            error => {
              console.log("Error getting certText : ")
              console.log(JSON.stringify(error))
            }
          )
      },
      error => {
        console.log("Platform error")
      }
    )

  }

  public getSession() {
    this.storage.get('user')
      .then(
        data => {
          console.log("Got user")
          console.log(JSON.stringify(data));
          this.user = data;
          if (this.user != null) {

            let user_id = (this.user.id_User != null ? this.user.id_User : this.user.id)

            this.events.publish('user:logedIn', this.user);
            console.log(JSON.stringify(JSON.parse(this.user.full_adresse).place_id))


            console.log("User id :" + user_id)

            let home = HomePage;
            this.navCtrl.setRoot(home, {
              user: this.user
            },
              {
                animate: true,
                animation: 'ios-transition',
                direction: 'forward',
                duration: 500
              }
            );

            this.updateUserInfo(user_id)

            this.backgroundMode.enable();

          }
          else {

            this.backgroundMode.disable()
            this.navCtrl.setRoot(DentartPage,
              {},
              {
                animate: true,
                animation: 'ios-transition',
                direction: 'forward',
                duration: 500
              }
            )
          }
        },
        error => {
          console.log(JSON.stringify(error));
        }
      )
  }

  public async getListNotifications() {
    //listFormations
    this.load = 1;
    let user_id = (this.user.id_User != null ? this.user.id_User : this.user.id)
    await this.http.get(this.globals.variables.urls.getNotifications,
      {
        "user_id": "" + user_id,
        "limit": "20",
        "offset": "" + this.notifOffset
      },
      {})
      .then(data => {
        let res = JSON.parse(data.data);

        let notifs = [];

        if (res.length <= 0) {
          this.load = 0;
        }
        else {

          for (let f = 0; f < res.length; f++) {

            this.http.get(this.globals.variables.urls.getUserInfo,
              {
                "user_id": res[f].user_id
              },
              {})
              .then(
                data => {
                  let com_author = JSON.parse(data.data).info_utilisateur;
                  console.log("Com_User_Author")
                  console.log(JSON.stringify(com_author))
                  res[f].com_author = com_author
                  if (res[f].state == 0) {
                    let notif = {
                      id: res[f].id,
                      text: res[f].message,
                      sound: 'assets/beep/beep.mp3',
                      vibrate: true,
                      data: res[f],
                      foreground: true
                    }
                    notifs.push(notif);
                    this.localNotifications.schedule(notifs);
                    this.listNewNotifs.push(res[f])
                    this.updateNotifState(res[f], "3")
                    console.log("List des formations");
                  }
                  else {
                    this.listOldNotifs.push(res[f])
                  }

                  this.listForms.push(res[f]);

                  // if (res[f].state == 0) {
                  //   this.localNotifications.schedule(notifs);
                  // }
                  // this.updateNotifState(res[f], "3")


                  if (f == res.length - 1) {
                    this.load = 0;
                  }
                }
              )

          }
          this.notifOffset += 1
        }

      },
        error => {
          console.log(JSON.stringify(error));
          this.load = 0;
        });

  }

  public async getListNotificationsNewPost() {

    // this.http.get(this.globals.variables.urls.getNotificationsNewPost,

    await this.http.get(this.globals.variables.urls.getNotificationsNewPost,
      {
        "user_id": "" + (this.user.id_User != null ? this.user.id_User : this.user.id),
        "limit": "20",
        "offset": "" + this.notifNewPostOffset
      },
      {})
      .then(data => {
        let res = JSON.parse(data.data);

        console.log("Resultat list notif new post")
        console.log(JSON.stringify(res))

        let notifs = [];

        if (res.length <= 0) {
          this.load = 0;
        }
        else {

          for (let f = 0; f < res.length; f++) {

            this.http.get(this.globals.variables.urls.getUserInfo,
              {
                "user_id": res[f].user_id
              },
              {})
              .then(
                data => {
                  let com_author = JSON.parse(data.data).info_utilisateur;
                  console.log("Com_User_Author")
                  console.log(JSON.stringify(com_author))
                  res[f].com_author = com_author
                  if (res[f].state == 0) {
                    let notif = {
                      id: res[f].id,
                      text: res[f].message,
                      sound: 'assets/beep/beep.mp3',
                      vibrate: true,
                      data: res[f],
                      foreground: true
                    }
                    notifs.push(notif);
                    // this.localNotifications.schedule(notifs);
                    console.log("List des notifs");
                    this.listNewNotifs.push(res[f])
                    this.localNotifications.schedule(notifs);
                    this.updateNotifState(res[f], "3")
                  }
                  else {
                    this.listOldNotifs.push(res[f])
                  }

                  this.listForms.push(res[f]);

                  // if (res[f].state == 0)
                  // this.updateNotifState(res[f], "3")

                  if (f == res.length - 1) {
                    this.load = 0;
                  }
                }
              )

          }
          this.notifNewPostOffset += 1
        }

      },
        error => {
          console.log(JSON.stringify(error));
          this.load = 0;
        });


  }

  public getListNewNotifications() {
    //listFormations
    this.http.get(this.globals.variables.urls.getNotifications,
      {
        "user_id": "" + (this.user.id_User != null ? this.user.id_User : this.user.id),
        "limit": "10"
      },
      {})
      .then(data => {
        let res = JSON.parse(data.data);
        console.log("List des nouveau notifications");
        let notifs = [];
        for (let g = 0; g < res.length; g++) {
          for (let k = 0; k < this.listForms.length; k++) {
            if (res[g].notif_id == this.listForms[k].notif_id) {
              break;
            }
            else if (res[g].notif_id != this.listForms[k].notif_id && k == (this.listForms.length - 1)) {
              this.listForms.splice(0, 0, res[g]);

              this.http.get(this.globals.variables.urls.getUserInfo,
                {
                  "user_id": res[g].user_id
                },
                {})
                .then(
                  data => {

                    let com_author = JSON.parse(data.data).info_utilisateur;
                    console.log("Com_User_Author")
                    console.log(JSON.stringify(com_author))
                    res[g].com_author = com_author

                    let notif = {
                      id: res[g].id,
                      text: res[g].message,
                      sound: 'assets/beep/beep.mp3',
                      vibrate: true,
                      data: res[g],
                      foreground: true
                    }
                    notifs.push(notif);
                    this.localNotifications.schedule(notifs);
                    this.listForms.push(res[g]);

                    if (res[g].state == 0)
                      this.updateNotifState(res[g], "3")
                  }
                )
            }
          }
        }

        // this.localNotifications.schedule(notifs);

        // setTimeout(() => {
        //   this.getListNewNotifications()
        // }, 5000);

      },
        error => {
          console.log(JSON.stringify(error));
          //this.load = 0;
        });
  }

  public updateNotifState(notif, state) {
    this.http.get(this.globals.variables.urls.updateNotifState,
      {
        "notification_id": notif.notif_id,
        "state": state

      },
      {})
      .then(data => {
        // notif.state = state;
        console.log("State of notif updated");
      },
        error => {
          console.log("Error updating notif state");
        });
  }

  public updateUserInfo(user_id) {
    this.http.get(this.globals.variables.urls.getUserInfo,
      {
        "user_id": "" + user_id
      },
      {}
    )
      .then(
        (data) => {
          let res = JSON.parse(data.data)
          console.log("User info to update")
          console.log(JSON.stringify(res))

          this.saveSession(res.info_utilisateur)


        },
        (error) => {
          console.log("Error updating user info")
          console.log(JSON.stringify(error))
          this.saveSession(this.user)
        }
      )
  }

  public saveSession(user1) {
    user1.avatarB64 = this.user.avatarB64
    this.storage.set('user', user1)
      .then(data => {
        //
      },
        error => {
          console.log(JSON.stringify(error));
        });

  }


}



