import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, Platform } from 'ionic-angular';
import { Toast } from '@ionic-native/toast';
import { GlobalsProvider } from '../../providers/globals/globals';
import { HTTP } from '@ionic-native/http';
import { MailtoPage } from '../mailto/mailto';
import { CallNumber } from '@ionic-native/call-number';
import { BrowserTab } from '@ionic-native/browser-tab';

/**
 * Generated class for the FormationDetailsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-formation-details',
  templateUrl: 'formation-details.html',
})
export class FormationDetailsPage {

  formation: any = {};
  user: any = {};

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private toast: Toast,
    private globals: GlobalsProvider,
    private http: HTTP,
    private modalCtrl: ModalController,
    private platform: Platform,
    private callNumber: CallNumber,
    private browserTab: BrowserTab
  ) {

    this.user = navParams.get('user');
    this.formation = navParams.get('formation');
    console.log("Phone for the formation : " + this.formation.telephone)
    let phoneOwner = this.formation.telephone != "" && this.formation.telephone != null ? this.formation.telephone : (this.formation.user_infos ? this.formation.user_infos.phone : this.formation.author_infos.phone)
    this.formation.phoneOwner = phoneOwner
    console.log("Phone for the formation : " + this.formation.phoneOwner)

    platform.registerBackButtonAction(() => {
      navCtrl.pop();
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FormationDetailsPage');
  }

  public retour() {
    this.navCtrl.pop();
  }

  public report() {
    //
    //repportFaQ
    this.http.post(this.globals.variables.urls.repportFaQ, {
      "user_id": this.user.id_User != null ? this.user.id_User : this.user.id,
      "post_id": this.formation.id
    },
      {})
      .then(
        data => {
          let res = JSON.parse(data.data);
          console.log(JSON.stringify(res));
          if (res.status == "ok") {
            if (this.formation.user_report == 0) {
              this.formation.user_report = 1;
              this.toast.show("Vous avez signalé cette formation", "2000", "bottom").subscribe(
                toast => {
                  console.log(toast);
                }
              );
            }
            else {
              this.formation.user_report = 0;
              this.toast.show("Votre signalisation a été annulée", "2000", "bottom").subscribe(
                toast => {
                  console.log(toast);
                }
              );
            }

          }
          else {
            console.log(JSON.stringify(res));
          }
          //console.log(JSON.stringify(data.data));
        },
        error => {
          console.log(JSON.stringify(error));
        }
      );
  }

  public showMailTo() {
    let mailto = this.modalCtrl.create(MailtoPage, {
      user: this.user,
      ad: this.formation,
      mail: this.formation.user_infos.user_email,
      post_id: this.formation.id,
      object: this.formation.title
    });
    mailto.onDidDismiss(data => {
      this.platform.registerBackButtonAction(() => {
        this.navCtrl.pop();
      });
      console.log(JSON.stringify(data));
    });
    mailto.present();
  }

  public callAuthor(phone) {
    console.log(phone);
    this.callNumber.isCallSupported()
    .then(
      data => {
        alert("Call suport : " + data)
      }
    )
    .catch(
      error => {
        console.log(JSON.stringify(error))
      }
    )
    this.callNumber.callNumber(phone, true)
      .then(res => console.log('Launched dialer!', res))
      .catch(err => console.log('Error launching dialer', err));
  }

  public openLink(url) {
    this.browserTab.isAvailable()
      .then(isAvailable => {
        if (isAvailable) {
          this.browserTab.openUrl(url);
        } else {
          this.toast.show("Navigateur non disponible", "2000", "bottom").subscribe(
            toast => {
              console.log(toast);
            }
          );
        }
      });
  }

}



