import { Component, ViewChild } from '@angular/core';
import { Platform, Events, Nav, App } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { DentartPage } from '../pages/dentart/dentart';
import { File } from '@ionic-native/file';
import { Network } from '@ionic-native/network';
import { NonetworkPage } from '../pages/nonetwork/nonetwork';
import { HTTP } from '@ionic-native/http';
import { GlobalsProvider } from '../providers/globals/globals';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { AuthloadingPage } from '../pages/authloading/authloading';
import { ConnexionPage } from '../pages/connexion/connexion';
import { Storage } from '@ionic/storage';


@Component({
  templateUrl: 'app.html'
})
export class MyApp {

  @ViewChild(Nav) nav: Nav;

  load: number = 0;

  rootPage: any = AuthloadingPage;

  user: any;

  isDentArt: number = 0;

  listForms: Array<any> = [];

  notifTimeout: any;

  constructor(
    platform: Platform,
    statusBar: StatusBar,
    splashScreen: SplashScreen,
    events: Events,
    private file: File,
    private network: Network,
    private http: HTTP,
    private globals: GlobalsProvider,
    private localNotifications: LocalNotifications,
    private app: App,
    private storage: Storage,
  ) {
    // this.backgroundMode.enable();

    platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();

      platform.registerBackButtonAction(() => {
        let nav = this.app.getActiveNav();
        if (nav.canGoBack()) { //Can we go back?
          nav.pop();
        } else {
          platform.exitApp(); //Exit from app
        }
      });


    });

    events.subscribe("user:logedIn", (userIn) => {
      this.user = userIn;
      // this.updateSessionInfos()
    });
    events.subscribe("user:logedOut", (userOut) => {
      this.user = null;
      this.testConnectedUser(userOut);
      clearTimeout(this.notifTimeout);

    });

    events.subscribe("app:exit", () => {
    })

    this.testConnectivity();

    if (this.user != null && this.user.id_User != null) {
    }

  }

  public testConnectedUser(user) {

    this.file.checkDir(this.file.dataDirectory, 'user')
      .then(
        data => {
          console.log("User directory found.");
          console.log(JSON.stringify(data));
          this.file.checkFile(this.file.dataDirectory + "user", "user.json")
            .then(
              data => {
                console.log("File user exist");
                this.file.removeFile(this.file.dataDirectory + "user", "user.json")
                  .then(
                    data => {
                      console.log("User loged out and removed");
                    },
                    error => {
                      console.log("User loged out but not removed");
                    }
                  );
              },
              error => {
                console.log("File user does not exist");
                this.file.createFile(this.file.dataDirectory + "user", "user.json", true)
                  .then(
                    data => {
                      this.file.removeFile(this.file.dataDirectory + "user", "user.json")
                        .then(
                          data => {
                            console.log("User loged out and removed");
                          },
                          error => {
                            console.log("User connected but not removed");
                          }
                        );
                    },
                    error => {
                      console.log("Can not create file user");
                    }
                  );
              }
            );
        },
        error => {
          console.log("User directory not found.");
          console.log(JSON.stringify(error));
          this.file.createDir(this.file.dataDirectory, "user", true)
            .then(
              data => {
                console.log("User directory creadted");
                this.file.createFile(this.file.dataDirectory + "user", "user.json", true)
                  .then(
                    data => {
                      this.file.removeFile(this.file.dataDirectory + "user", "user.json")
                        .then(
                          data => {
                            console.log("User loged out and removed");
                          },
                          error => {
                            console.log("User connected but not removed");
                          }
                        );
                    },
                    error => {
                      console.log("Can not create file user");
                    }
                  );
              },
              error => {
                console.log("Can not create user directory");
              }
            );
        }
      );

  }

  public testConnectivity() {
    console.log("Testing network");
    if (this.network.type == 'none' || this.network.type == 'unknown') {
      this.rootPage = NonetworkPage;
      this.isDentArt = 1;
    }
    else {

      if (this.isDentArt == 1) {
        this.rootPage = AuthloadingPage;
        this.isDentArt = 0;
      }
    }
    setTimeout(() => {
      this.testConnectivity();
    }, 5000);
  }

}




