import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Events } from 'ionic-angular';
import { HomePage } from '../home/home';
import { RecoverpasswdPage } from '../recoverpasswd/recoverpasswd';
import { HTTP } from '@ionic-native/http';
import { GlobalsProvider } from '../../providers/globals/globals';
import { File } from '@ionic-native/file';
import { Keyboard } from '@ionic-native/keyboard';
import { Storage } from '@ionic/storage';
import { AndroidPermissions } from '@ionic-native/android-permissions';

/**
 * Generated class for the ConnexionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-connexion',
  templateUrl: 'connexion.html',
})
export class ConnexionPage {

  mail: string = '';
  password: string = '';
  user: any;
  partageDB: any;

  isBtnConnect: boolean = false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private http: HTTP,
    private globals: GlobalsProvider,
    private alertCtrl: AlertController,
    private events: Events,
    private file: File,
    private keyboard: Keyboard,
    private storage: Storage,
    private androidPermissions: AndroidPermissions
  ) {

    //this.keyboard.show();

    console.log(this.file.dataDirectory);

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ConnexionPage');
    this.checkReadWritePermissions()
  }
  public savePubs(){
    let pubFinal = [];
    this.http.post(this.globals.variables.urls.getListPub + "?offset=0&limit=1",
      {},
      {})
      .then(
        data => {

          let res = JSON.parse(data.data);
          console.log("Nombre Pubs: "+res.length)
          if (res.length > 0) {

            for (let r = 0; r < res.length; r++) {
              res[r].type = 'ad'
              let char70 = "";
              char70 = res[r].description + "...";
              res[r].char70 = char70;
              pubFinal.push(res[r]);
            }
            this.storage.set('pubs', JSON.stringify(pubFinal));            
          }
        },
        error => {
          console.log(JSON.stringify(error));
        }
      );
  }
  public savePartages(user_id){
    let partageFinal = [];
    this.storage.set('partages', ""); 
    this.http.post(this.globals.variables.urls.listFaQ + '?offset=0&category=&user_id=' + user_id + '&limit=5',
      {},
      {})
      .then(
        data => {
          console.log()
          let res = JSON.parse(data.data);
          console.log("Nombre Partages: "+res.length)
          if (res.length > 0) {

            for (let r = 0; r < res.length; r++) {
              res[r].type = 'faq'
              let char70 = "";
              char70 = res[r].description + "...";
              res[r].char70 = char70;
              res[r].instore = 1;
              partageFinal.push(res[r]);
            }
            console.log("***********PARTAGEFINAL******************")
            console.log(JSON.stringify(partageFinal))
            this.storage.set('partages', JSON.stringify(partageFinal));            
          }
        },
        error => {
          console.log(JSON.stringify(error));
        }
      );
  }
  public saveSession(user) {
    this.storage.set('user', user)
      .then(data => {
        let home = HomePage;
        this.navCtrl.setRoot(home, {
          user: this.user
        },
          {
            animate: true,
            animation: 'ios-transition',
            direction: 'forward',
            duration: 500
          });
      },
        error => {
          console.log(JSON.stringify(error));
        });

  }

  public testConnectedUser(user) {

    this.file.checkDir(this.file.dataDirectory, 'user/')
      .then(
        data => {
          console.log("User directory found.");
          console.log(JSON.stringify(data));
          this.file.checkFile(this.file.dataDirectory + "user/", "user.json")
            .then(
              data => {
                this.file.writeFile(this.file.dataDirectory + "user/", "user.json", JSON.stringify(user), { replace: true })
                  .then(
                    data => {
                      let home = HomePage;
                      this.navCtrl.setRoot(home, {
                        user: this.user
                      },
                        {
                          animate: true,
                          animation: 'ios-transition',
                          direction: 'forward',
                          duration: 500
                        });
                    },
                    error => {
                      console.log("User connected but not saved");
                    }
                  );
              },
              error => {
                this.file.writeFile(this.file.dataDirectory + "user/", "user.json", JSON.stringify(user), { replace: true })
                  .then(
                    data => {
                      let home = HomePage;
                      this.navCtrl.setRoot(home, {
                        user: this.user
                      },
                        {
                          animate: true,
                          animation: 'ios-transition',
                          direction: 'forward',
                          duration: 500
                        });
                    },
                    error => {
                      console.log("Can not create file user");
                    }
                  );
              }
            );
        },
        error => {
          console.log(JSON.stringify(error));
          this.file.createDir(this.file.dataDirectory, "user/", true)
            .then(
              data => {
                this.file.writeFile(this.file.dataDirectory + "user/", "user.json", JSON.stringify(user), { replace: true })
                  .then(
                    data => {
                      let home = HomePage;
                      this.navCtrl.setRoot(home, {
                        user: this.user
                      },
                        {
                          animate: true,
                          animation: 'ios-transition',
                          direction: 'forward',
                          duration: 500
                        });
                    },
                    error => {
                      console.log("Can not create file user");
                    }
                  );
              },
              error => {
                console.log("Can not create user directory");
              }
            );
        }
      );


  }

  public checkReadWritePermissions() {

    this.androidPermissions.checkPermission(this.androidPermissions.PERMISSION.FOREGROUND_SERVICE)
      .then(
        result => {
          if (!result.hasPermission) {
            this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.FOREGROUND_SERVICE)
            .then(
              resultRequest => {
                console.log(JSON.stringify(resultRequest))
              },
              errorRequest => {
                console.log(JSON.stringify(errorRequest))
              }
            )
          }
        },
        error => {
          console.log("Error checking foreground service permission")
          this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.FOREGROUND_SERVICE)
          .then(
            resultRequest => {
              console.log(JSON.stringify(resultRequest))
            },
            errorRequest => {
              console.log(JSON.stringify(errorRequest))
            }
          )
        }
      )

    this.androidPermissions.checkPermission(this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE)
      .then(
        result => {
          if (!result.hasPermission) {
            this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE)
          }
        },
        error => {
          this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE)
        }
      )
    this.androidPermissions.checkPermission(this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE)
      .then(
        result => {
          if (!result.hasPermission) {
            this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE)
          }
        },
        error => {
          console.log("Error write pernission !")
          this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE)
        }
      )

  }

  public connect() {

    this.isBtnConnect = true;

    if (!(/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.mail))) {
      this.isBtnConnect = false;
      let alert = this.alertCtrl.create({
        title: 'Information',
        subTitle: 'Votre e-mail est invalide.',
        buttons: ['Ok']
      });
      alert.present();
      this.isBtnConnect = false;
    }
    else if (this.password.length < 8) {
      this.isBtnConnect = false;
      let alert = this.alertCtrl.create({
        title: 'Information',
        subTitle: 'Votre mot de passe doit contenir au minimum 8 caractères.',
        buttons: ['Ok']
      });
      alert.present();
      this.isBtnConnect = false;
    }
    if (/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.mail) &&
      this.password.length >= 8) {

      this.http.post(this.globals.variables.urls.loginURL,
        {
          "mail": this.mail,
          "password": this.password
        }, {})
        .then(data => {
          let res = JSON.parse(data.data);

          if (res.etat_connexion == 'connexion-ok') {
            this.user = res.info_utilisateur;
            // this.savePartages(this.user.id_User);
            this.savePubs();
            const avatar = this.user.avatar != null ? this.user.avatar : this.user.photo
            const profil_name = "profil_" + (new Date().getTime()) + ".jpeg"
            this.http.downloadFile(avatar,
              {},
              {},
              this.file.dataDirectory + profil_name
            )
              .then(
                data => {
                  this.file.readAsDataURL(this.file.externalDataDirectory, profil_name)
                    .then(
                      data => {
                        this.user.avatarB64 = data
                        this.saveSession(this.user);
                      },
                      error => {
                        console.log("Error reading profil from download")
                        this.saveSession(this.user);
                      }
                    )
                },
                error => {
                  console.log("Error saving profil from download")
                  this.saveSession(this.user);
                }
              )
          }
          else if (res.etat_connexion == 'non-ok') {
            this.isBtnConnect = false;
            let alert = this.alertCtrl.create({
              title: 'Information',
              subTitle: 'Votre email ou mot de passe est incorrect.',
              buttons: ['Ok']
            });
            alert.present();
          }
        }, error => {
          this.isBtnConnect = false;
          console.log(JSON.stringify(error));
          let alert = this.alertCtrl.create({
            title: 'Information',
            subTitle: 'Problème de connexion internet.',
            buttons: ['Ok']
          });
          alert.present();
        });

    }

  }

  public changeMail(event) {

    let value = event.target.value;

  }

  public forgetPasswd() {
    let recov = RecoverpasswdPage;
    this.navCtrl.push(recov);
  }

  public showKeyboard() {
    this.keyboard.show();
  }

  public precedent() {
    this.navCtrl.pop();
  }

}



