import { Component, ViewChild } from '@angular/core';
import { NavController, ViewController } from 'ionic-angular';
import { AngularCropperjsComponent } from 'angular-cropperjs';
import { CameraOptions, Camera } from '@ionic-native/camera';

/**
 * Generated class for the Cropper2Page page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-cropper2',
  templateUrl: 'cropper2.html',
})
export class Cropper2Page {
  @ViewChild('angularCropper') public angularCropper: AngularCropperjsComponent;
  cropperOptions: any;
  croppedImage = null;

  myImage = null;
  scaleValX = 1;
  scaleValY = 1;

  constructor(
      public navCtrl: NavController,
      private viewCtrl: ViewController, 
      private camera: Camera
      ) {
    this.cropperOptions = {
      aspectRatio: 1 / 1,
      dragMode: 'move',
      modal: true,
      guides: true,
      highlight: false,
      background: true,
      autoCrop: true,
      autoCropArea: 0.8,
      responsive: false
    };
  }

  cropperTouchStart(event) {
    event.stopPropagation();
    event.preventDefault(); 
  }

  captureImage() {
    const options: CameraOptions = {
      quality: 100,
      targetWidth: 800,
      targetHeight: 800,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      sourceType: this.camera.PictureSourceType.CAMERA,
      correctOrientation: true
    }

    this.camera.getPicture(options).then((imageData) => {
      if (imageData != null) {
        this.myImage = 'data:image/jpeg;base64,' + imageData;
      } else {
        this.viewCtrl.dismiss(null);
      }
    }, (err) => {
      console.log("************captureImage****************")
      console.log(err);
    });
  }

  captureGallery() {
    const options: CameraOptions = {
      quality: 100,
      targetWidth: 800,
      targetHeight: 800,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      correctOrientation: true
    }
      this.camera.getPicture(options).then((imageData) => {
        if (imageData != null) {
          this.myImage = 'data:image/jpeg;base64,' + imageData;
        } else {
          this.viewCtrl.dismiss(null);
        }
      }, (err) => {
        console.log("************captureGallery****************")
        console.log(err);
      });
  }

  reset() {
    this.angularCropper.cropper.reset();
  }

  clear() {
    // this.angularCropper.cropper.clear();
    this.viewCtrl.dismiss(null);
  }

  rotate() {
    this.angularCropper.cropper.rotate(90);
  }

  zoom(zoomIn: boolean) {
    let factor = zoomIn ? 0.1 : -0.1;
    this.angularCropper.cropper.zoom(factor);
  }

  scaleX() {
    this.scaleValX = this.scaleValX * -1;
    this.angularCropper.cropper.scaleX(this.scaleValX);
  }

  scaleY() {
    this.scaleValY = this.scaleValY * -1;
    this.angularCropper.cropper.scaleY(this.scaleValY);
  }

  move(x, y) {
    this.angularCropper.cropper.move(x, y);
  }

  save() {
    let imageData: string = this.angularCropper.cropper.getCroppedCanvas().toDataURL('image/jpeg', (100 / 100));
    // this.croppedImage = croppedImgB64String;
    if (JSON.stringify(imageData) != "[]") {
        console.log(JSON.stringify(imageData).substr(0, 20))
        let img = {
          id: new Date().getTime(),
          url: imageData,
          data: { nativeURL: imageData },
          base64: imageData,
          source: "galery"
        };
        this.viewCtrl.dismiss(img);
      }
      else {
        this.viewCtrl.dismiss(null);
      }
  }
}



