  public getListCat() {
    this.http.post(this.globals.variables.urls.listAnnonceCat, {}, {})
      .then(
        data => {

          this.listCat = JSON.parse(data.data);
          this.load = 0;
          console.log(JSON.stringify(this.listCat));
        },
        error => {
          this.load = 0;
          console.log(JSON.stringify(error));
        }
      );
  }

  public getListAds(infiniteScroll) {
    console.log("Getting Ads lists.");
    let id_user = this.user.id_User != null ? this.user.id_User : this.user.id;

    this.http.post(this.globals.variables.urls.listAdByUser + "?id_user=" + id_user + "&offset=" + this.paging + "&category=" + this.selectedCat.id + "&pertinence=" + this.pertinence + "&date=DESC", {}, {})
      .then(
        data => {

          let res = JSON.parse(data.data);
          if (res.length <= 0) {
            this.load = 0;
            if (infiniteScroll != null) {
              console.log('Async operation has ended');
              infiniteScroll.complete();
            }
          }
          else {
            this.load = 0;
            for (let r = 0; r < res.length; r++) {
              let date_fr = res[r].date.charAt(8) + res[r].date.charAt(9) + "/" + res[r].date.charAt(5) + res[r].date.charAt(6) +
                "/" + res[r].date.charAt(0) + res[r].date.charAt(1) + res[r].date.charAt(2) + res[r].date.charAt(3);
              res[r].date_fr = date_fr;
              // res[r].date_fr = date_fr;

              res[r].images_count = 0;
              if (res[r].ad_image_1 != '' && res[r].ad_image_1 != null) res[r].images_count += 1;
              if (res[r].ad_image_2 != '' && res[r].ad_image_2 != null) res[r].images_count += 1;
              if (res[r].ad_image_3 != '' && res[r].ad_image_3 != null) res[r].images_count += 1;
              if (res[r].ad_image_4 != '' && res[r].ad_image_4 != null) res[r].images_count += 1;

              let char70 = "";
              if (res[r].description.length > 70) {
                char70 = "" + res[r].description.substr(0, 69) + "...";
              }
              else {
                char70 = res[r].description;
              }

              res[r].char70 = char70;
              this.listAdsSorted.push(res[r]);
              
            }
            if (infiniteScroll != null) {
              console.log('Async operation has ended');
              infiniteScroll.complete();
            }
            console.log("Getting user info (ad Author)");
          }

        },
        error => {
          this.load = 0;
          console.log(JSON.stringify(error));
        }
      );
  }

