import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ModalController, AlertController } from 'ionic-angular';
import { CertphotoPage } from '../certphoto/certphoto';
import { Faqadd1Page } from '../faqadd1/faqadd1';
import { GlobalsProvider } from '../../providers/globals/globals';
import { HTTP } from '@ionic-native/http';
import { Toast } from '@ionic-native/toast';
import { PhotopopupPage } from '../photopopup/photopopup';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the PopupcertPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-popupcert',
  templateUrl: 'popupcert.html',
})
export class PopupcertPage {

  user: any = {};
  load: number = 0;
  cert_text: string = null

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    private modalCtrl: ModalController,
    private alertCtrl: AlertController,
    private globals: GlobalsProvider,
    private http: HTTP,
    private toast: Toast,
    private storage: Storage
  ) {

    this.user = navParams.get('user');
    this.getCertTextFromStorage()

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PopupcertPage');
  }

  public async getCertTextFromStorage() {
    this.storage.get("cert_text")
      .then(
        certtext => {
          console.log("Cert text in the popup certificate : ")
          console.log(certtext)
          this.cert_text = certtext
        },
        error => {
          console.log("Error getting local cert text")
        }
      )


  }

  public dismiss() {
    let data = { 'foo': 'bar' };
    this.viewCtrl.dismiss(data);
  }

  public prendrePhoto() {
    let takePic = this.modalCtrl.create(PhotopopupPage, {
      source: 2,
      user: this.user,
      title: "Envoyer"
    });
    takePic.onDidDismiss(data => {
      //this.viewCtrl.dismiss();
      if (data != null) {
        this.load = 1;
        this.uploadCertPhotoB64(data);
      }
      else {
        this.toast.show("Pas de photo prise !", "3000", "bottom")
          .subscribe(
            t => console.log(JSON.stringify(t))
          );
      }

      // this.viewCtrl.dismiss();
    })
    takePic.present();
  }

  public uploadCertPhoto(img) {
    //certificate
    this.http.uploadFile(this.globals.variables.urls.certificate,
      {
        "user_id": "" + (this.user.id_User != null ? this.user.id_User : this.user.id)
      },
      {},
      img.data.nativeURL,
      'file')
      .then(
        data => {
          this.load = 0;
          let res = JSON.parse(data.data);
          if (res.status == "OK") {
            let alert = this.alertCtrl.create({
              title: 'Information !',
              message: 'Votre photo a été téléchargée',
              buttons: ['Ok']
            });
            alert.present();
            this.viewCtrl.dismiss({
              etat: "uploaded"
            });
          }
          console.log(JSON.stringify(data.data));
        },
        error => {
          let alert = this.alertCtrl.create({
            title: 'Information !',
            message: 'Une erreur c\'est produite lors de la téléchargement',
            buttons: ['Ok']
          });
          alert.present();
          this.viewCtrl.dismiss({
            etat: "error"
          });
          console.log(JSON.stringify(error));
        }
      );
  }

  public uploadCertPhotoB64(img) {
    //certificate
    this.http.post(this.globals.variables.urls.certificate,
      {
        "user_id": "" + (this.user.id_User != null ? this.user.id_User : this.user.id),
        // "image": img.data.nativeURL
      },
      {},
      // img.data.nativeURL,
      // 'file'
    )
      .then(
        data => {

          this.http.post(this.globals.variables.urls.addImageCertificateB64,
            {
              "user_id": "" + (this.user.id_User != null ? this.user.id_User : this.user.id),
              "image": img.data.nativeURL,
              "source": data.data.source
            },
            {})
            .then(
              data => {
                this.load = 0;
                let res = JSON.parse(data.data);
                if (res.status == "OK") {
                  let alert = this.alertCtrl.create({
                    title: 'Information !',
                    message: 'Votre photo a été téléchargée',
                    buttons: ['Ok']
                  });
                  alert.present();
                  this.viewCtrl.dismiss({
                    etat: "uploaded"
                  });
                }
                console.log(JSON.stringify(data.data));
              },
              error => {
                this.load = 0;
                let alert = this.alertCtrl.create({
                  title: 'Information !',
                  message: 'Une erreur c\'est produite lors de la téléchargement',
                  buttons: ['Ok']
                });
                alert.present();
                this.viewCtrl.dismiss({
                  etat: "error"
                });
                console.log(JSON.stringify(error));
              }
            )

        },
        error => {
          this.load = 0;
          console.log()
          let alert = this.alertCtrl.create({
            title: 'Information !',
            message: 'Une erreur c\'est produite, \n Veuillez réessayer à votre prochaine connexion SVP !',
            buttons: ['Ok']
          });
          alert.present();
          this.viewCtrl.dismiss({
            etat: "error"
          });
        }
      );
  }

}



