import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Platform, ModalController } from 'ionic-angular';
import { SocialSharing } from '@ionic-native/social-sharing';
import { Instagram } from '@ionic-native/instagram';
import { HTTP } from '@ionic-native/http';

/**
 * Generated class for the SharemodalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sharemodal',
  templateUrl: 'sharemodal.html',
})
export class SharemodalPage {

  canShareViaInstagram: boolean = false
  canShareViaFacebook: boolean = false
  canShareViaWhatsapp: boolean = false
  canShareViaTwitter: boolean = false

  file: Array<any> = []
  bodyText: string = ""
  link: string = ""
  perma_link: string = ""

  dentart_icon = "https://www.dentart.fr/wp-content/uploads/2018/11/logo.jpg"

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private viewCtrl: ViewController,
    private platform: Platform,
    private socialSharing: SocialSharing,
    private modalCtrl: ModalController,
    private instagram: Instagram,
    private http: HTTP
  ) {

    this.file = navParams.get("file")
    this.bodyText = navParams.get("bodyText")
    this.link = navParams.get("link")
    this.perma_link = navParams.get("perma_link")

    platform.registerBackButtonAction(() => {
      this.viewCtrl.dismiss({
        shared: false
      })
    })

    this.socialSharing.canShareVia('instagram')
      .then(data => {
        console.log("Can we share via instagram");
        console.log(JSON.stringify(data));
        this.canShareViaInstagram = true;
      },
        error => {
          console.log("We can't share via instagram");
          console.log(JSON.stringify(error));
        });

    this.socialSharing.canShareVia('facebook')
      .then(data => {
        console.log("Can we share via facebook");
        console.log(JSON.stringify(data));
        this.canShareViaFacebook = true;
      },
        error => {
          console.log("We can't share via facebook");
          console.log(JSON.stringify(error));
        });

    this.socialSharing.canShareVia('com.facebook.lite')
      .then(data => {
        console.log("Can we share via facebook lite");
        console.log(JSON.stringify(data));
        this.canShareViaFacebook = true;
      },
        error => {
          console.log("We can't share via facebook lite");
          console.log(JSON.stringify(error));
        });

    this.socialSharing.canShareVia('whatsapp')
      .then(data => {
        console.log("Can we share via whatsapp");
        console.log(JSON.stringify(data));
        this.canShareViaWhatsapp = true;
      },
        error => {
          console.log("We can't share via whatsapp");
          console.log(JSON.stringify(error));
        });

    this.socialSharing.canShareVia('twitter')
      .then(data => {
        console.log("Can we share via twitter");
        console.log(JSON.stringify(data));
        this.canShareViaTwitter = true;
      },
        error => {
          console.log("We can't share via twitter");
          console.log(JSON.stringify(error));
        });


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SharemodalPage');

  }

  public share() {
    this.socialSharing.share(this.bodyText, "DentArt", null, this.perma_link)
      .then(
        data => {
          this.viewCtrl.dismiss({
            shared: true
          })
        }
      )
      .catch(
        error => {
          this.viewCtrl.dismiss({
            shared: false
          })
        }
      )
  }

  public shareViaInstagramStory() {
    let urlFileToShare = ""
    if (this.file.length > 0) {
      urlFileToShare = this.file[0]
    }
    if (urlFileToShare != "") {
      let img = this.http.downloadFile(urlFileToShare, {}, {}, new Date().getTime() + ".png")
        .then(
          entryFile => { },
          error => {
            console.log("Error downloading the image")
            console.log(JSON.stringify(error))
          }

        )
      this.instagram.share(null, this.bodyText)
        .then(() => {
          console.log('Shared!')
        }
        )
        .catch(
          (error: any) => {
            console.error(JSON.stringify(error))
          }
        );
    }

  }

  public shareViaFacebookSharer() {

    console.log("Body Text")
    console.log(this.escapeHtml2(this.bodyText))
    console.log("Files : ")
    console.log(JSON.stringify(this.file))
    let quote = this.escapeHtml2(this.bodyText)

    let fbHostSharer = "https://www.facebook.com/sharer/sharer.php?kid_directed_site=0&sdk=joey&u=" + this.perma_link + "&display=popup&ref=plugin&src=share_button&quote=" + quote

    let urlToOpen = "https://www.facebook.com/sharer/sharer.php?kid_directed_site=0&sdk=joey&u=http%3A%2F%2Fdentart.devforge.eu%2Ffaqs%2F28290-2%2F&display=popup&ref=plugin&src=share_button"

    console.log(fbHostSharer)

    let sharingWin = window.open(fbHostSharer , '_blank')

    this.viewCtrl.dismiss({
      shared: true
    })

  }

  public escapeHtml2(unsafe) {
    return unsafe
      .replace(/&/g, "&amp;")
      .replace(/</g, "&lt;")
      .replace(/>/g, "&gt;")
      .replace(/"/g, "&quot;")
    // .replace(/'/g, "&#039;");
  }

  public escapeHtml(html) {
    var text = document.createTextNode(html);
    var p = document.createElement('div');
    p.innerText = html;
    return p.innerHTML;
  }

  public shareViaFacebook() {
    console.log(JSON.stringify(this.file))
    console.log(this.bodyText)
    console.log(this.link)
    let fileToShare = this.file.length > 0 ? this.file[0] : null
    let linkToShare = this.link != "" ? this.link : null
    this.socialSharing.shareViaFacebookWithPasteMessageHint(this.bodyText, null, fileToShare, this.bodyText)
      .then(
        data => {
          console.log("Shared in facebook ")
          console.log(JSON.stringify(data))
          this.viewCtrl.dismiss({
            shared: true
          })
        },
        error => {
          console.log("Error sharing to facebook")
          console.log(JSON.stringify(error))
          this.viewCtrl.dismiss({
            shared: false
          })
        }
      )

  }

  public shareViaTwitter() {
    console.log(JSON.stringify(this.file))
    console.log(this.bodyText)
    console.log(this.link)

    let fileToShare = this.file.length > 0 ? this.file[0] : null
    let linkToShare = this.link != "" ? this.link : null
    this.socialSharing.shareViaTwitter(this.bodyText, fileToShare, this.perma_link)
      .then(
        data => {
          console.log("Shared in Twitter ")
          console.log(JSON.stringify(data))
          this.viewCtrl.dismiss({
            shared: true
          })
        },
        error => {
          console.log("Error sharing to facebook")
          console.log(JSON.stringify(error))
          this.viewCtrl.dismiss({
            shared: false
          })
        }
      )
  }

  public shareViaWhatsapp() {
    console.log(JSON.stringify(this.file))
    console.log(this.bodyText)
    console.log(this.link)

    let fileToShare = this.file.length > 0 ? this.file[0] : null
    let linkToShare = this.link != "" ? this.link :
      this.socialSharing.shareViaWhatsApp(this.bodyText, fileToShare, this.perma_link)
        .then(
          data => {
            console.log("Shared in Whatsapp ")
            console.log(JSON.stringify(data))
            this.viewCtrl.dismiss({
              shared: true
            })
          },
          error => {
            console.log("Error sharing to facebook")
            console.log(JSON.stringify(error))
            this.viewCtrl.dismiss({
              shared: false
            })
          }
        )
  }

  public shareViaInstagram() {
    console.log(JSON.stringify(this.file))
    console.log(this.bodyText)
    console.log(this.link)

    let fileToShare = this.file.length > 0 ? this.file[0] : null
    let linkToShare = this.link != "" ? this.link : null

    // let quote = " << " + this.perma_link + " >> "
    // quote += (fileToShare != null ? " << " + fileToShare + " >> " : "")
    let quote = ""
    // quote += (fileToShare != null ? " << " + fileToShare + " >> " : "")
    quote += this.bodyText + " "
    quote += " " + this.perma_link + " "
    this.socialSharing.shareViaInstagram(quote, null)
      .then(
        data => {
          console.log("Shared in Instagram ")
          console.log(JSON.stringify(data))
          this.viewCtrl.dismiss({
            shared: true
          })
        },
        error => {
          console.log("Error sharing to facebook")
          console.log(JSON.stringify(error))
          this.viewCtrl.dismiss({
            shared: false
          })
        }
      )
  }

  public shareViaSMS() {
    this.socialSharing.shareViaSMS(this.bodyText + " " + this.perma_link, "")
      .then(
        data => {
          this.viewCtrl.dismiss({
            shared: true
          })
        },
        error => {
          this.viewCtrl.dismiss({
            shared: false
          })
        }
      )
  }

  public shareViaGMAIL() {
    this.socialSharing.shareViaEmail(this.bodyText + " " + this.perma_link, "DentArt", [""], [""], [""], this.file)
      .then(
        data => {
          this.viewCtrl.dismiss({
            shared: true
          })
        },
        error => {
          this.viewCtrl.dismiss({
            shared: false
          })
        }
      )
  }

  public anniler() {
    this.viewCtrl.dismiss({
      shared: false
    })
  }

}



