  public updateUserDeviceTokenHelper = async (user_id, token, action) => {

    this.http.post(this.globals.variables.urls.updateUserDeviceToken + "?user_id=" + user_id +
      "&token=" + token + "&action=" + action,
      {
        user_id: user_id,
        token: token,
        action: action
      },
      {})
      .then(
        (data) => {
          console.log("Result from adding token")
          console.log(JSON.stringify(data.data))
        },
        (error) => {
          console.log("Result from adding token")
          console.log(JSON.stringify(error))
        }
      )
  }

  public requestAllPermissions() {
    this.androidPermissions.checkPermission(this.androidPermissions.PERMISSION.CAMERA).then(
      result => {
        console.log('Has permission?', result.hasPermission)
      },
      err => this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.CAMERA)
    );

    this.androidPermissions.checkPermission(this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE).then(
      result => {
        console.log('Has permission?', result.hasPermission)
      },
      err => this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE)
    );

    this.androidPermissions.checkPermission(this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE).then(
      result => {
        console.log('Has permission?', result.hasPermission)
      },
      err => this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE)
    );
  }

