  public openURL(url) {
    console.log(url);
    if (url != '') {
      this.browserTab.isAvailable()
        .then(isAvailable => {
          if (isAvailable) {
            this.browserTab.openUrl(url);
          } else {
            this.toast.show("Navigateur non disponible", "2000", "bottom").subscribe(
              toast => {
                console.log(JSON.stringify(toast));
              }
            );
          }
        });
    }
    else {
      this.toast.show("Pas d'URL pour cette pub", "2000", "bottom").subscribe(
        toast => {
          console.log(toast);
        }
      );
    }
  }

  public openMenu() {
    let menu = this.modalCtrl.create(MenuPage, {
      user: this.user
    },
      {
        enterAnimation: 'modal-translate-up-enter',
        leaveAnimation: 'modal-translate-up-leave'
      });
    //let menu = MenuPage;
    menu.onDidDismiss(page => {
      if (page.title == "Partages") {
        //
      }
      else if (page.title == "profil") {
        let adPage = UprofilPage;
        this.navCtrl.push(adPage, {
          user: this.user
        },
          {
            animate: true
          });
      }
      else if (page.title == "Annonces") {
        let adPage = AnnoncesPage;
        this.navCtrl.push(adPage, {
          user: this.user
        },
          {
            animate: true
          });

      }
      else if (page.title == "Formations") {
        let formPage = FormationsPage;
        this.navCtrl.push(formPage, {
          user: this.user
        },
          {
            animate: true
          });
      }
      else if (page.title == "News") {
        let actuPage = ASavoirPage;
        this.navCtrl.push(actuPage, {
          user: this.user
        },
          {
            animate: true
          });
      }
      else if (page.title == "Mes alertes") {
        let notif = NotificationsPage;
        this.navCtrl.push(notif, {
          user: this.user
        },
          {
            animate: true
          });
      }
      else if (page.title == 'mesannonces') {
        let notif = MesannoncesPage;
        this.navCtrl.push(notif, {
          user: this.user
        },
          {
            animate: true
          });
      }
      else if (page.title == "deconnexion") {

        this.events.publish('user:logedOut', this.user);
        this.load = 1;
        this.storage.set('partages', null)
        this.storage.set('user', null)
          .then(
            data => {
              console.log(JSON.stringify(data));
              this.storage.clear();
              setTimeout(() => {
                this.load = 0;
                this.navCtrl.setRoot(DentartPage, {}, {
                  animate: true
                })
              }, 500)
            },
            error => {
              console.log(JSON.stringify(error));
            });


      }
      else if (page.title == "") {
        //
      }
    });
    menu.present();
  }

