    public setAvatar() {

        this.load = 1

        let newphoto = this.modalCtrl.create(Cropper2Page, {
            source: "profil",
            user: this.user
        });

        newphoto.onDidDismiss(data => {

            this.platform.registerBackButtonAction(() => {
                this.viewCtrl.dismiss({
                    status: "no_update"
                });
            });

            if (data != null && data.url != null) {
                this.avatar = data;

                this.http.post(this.globals.variables.urls.editProfilURLB64,
                    {
                        "user_id": "" + (this.user.id_User != null ? this.user.id_User : this.user.id),
                        "image": data.data.nativeURL,
                        "source": data.data.source
                    },
                    {}
                    // ,
                    // newImage.substring(0, newImage.lastIndexOf('?')),
                    // 'file-avatar'
                )
                    .then(
                        data => {

                            this.load = 0

                            let upUser = JSON.parse(data.data).info_utilisateur
                            upUser.mail = upUser.user_email != null ? upUser.user_email : upUser.mail
                            this.user.avatar = upUser.avatar;
                            const avatar = this.user.avatar != null ? this.user.avatar : this.user.photo
                            this.http.downloadFile(avatar,
                                {},
                                {},
                                this.file.dataDirectory + "profil.jpeg"
                            )
                                .then(
                                    data => {
                                        console.log("Resultat du save profil")
                                        console.log(JSON.stringify(data))
                                        this.file.readAsDataURL(this.file.dataDirectory, "profil.jpeg")
                                            .then(
                                                data => {
                                                    this.user.avatarB64 = data
                                                    this.saveSession(this.user);
                                                }
                                            )
                                            this.user = upUser
                                            console.log("Res edit profil");
                                            console.log(JSON.stringify(data));
                                            console.log("Res edit profil");
                                            // this.viewCtrl.dismiss(this.selectedImg);
                                            this.toast.show("Votre profil est à jour", '3000', 'bottom').subscribe(
                                                toast => {
                                                    console.log(JSON.stringify(toast));
                                                }
                                            );
                                    },
                                    error => {
                                        console.log("Error saving profil from download")
                                        console.log(JSON.stringify(error))
                                    }
                                )
                            // this.saveSession(upUser)
                            
                        },
                        error => {
                            this.load = 0
                            console.log("Error uploading")
                            console.error(JSON.stringify(error));
                            // this.viewCtrl.dismiss();
                            let alert = this.alertCtrl.create({
                                title: 'Information !',
                                subTitle: 'Erreur de téléchargement de l\'image.',
                                buttons: ['Ok']
                            });
                            alert.present();
                        }
                    );
            }
            else {
                this.load = 0
            }


        });
        newphoto.present();

    }

