    public changeJob() {

        let filtre = this.modalCtrl.create(FaqfilterPage,
            {
                theme: this.jobs,
                //   listTyp: this.listType,
                user: this.user,
                title: "Profession",
                selectBefore: this.selectBefore,
                source: "profil_job"
            });

        filtre.onDidDismiss(
            (data) => {
                if (data.job != null && data.job.id != '') {
                    this.selectBefore = data.job
                    this.job = data.job.name;
                }

            })

        filtre.present()

    }

