    updateSearchResults() {
        console.log("Typing");
        if (this.autocomplete.input == '') {
            this.autocompleteItems = [];
            return;
        }
        this.GoogleAutocomplete.getPlacePredictions({ input: this.autocomplete.input },
            (predictions, status) => {
                if (predictions != null) {
                    this.autocompleteItems = [];
                    this.zone.run(() => {
                        predictions.forEach((prediction) => {
                            this.autocompleteItems.push(prediction);
                        });
                    });
                }

                console.log(this.autocompleteItems);
            });
    }

    public selectSearchResult(item) {
        console.log(JSON.stringify(item));
        this.full_ad_address = JSON.stringify(item);
        this.ad_address = item.description;
        this.autocomplete.input = item.description;
        this.autocompleteItems = [];
    }

    public emailTyping() {
        if (/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(this.mail)) {
            console.log("Mail valid.");
            this.validMail = 1;
        }
        else {
            console.log("Mail invalid.");
            this.validMail = 0;
        }
    }

    public passwdTyping() {
        if (this.newpassowrd.length >= 8) {
            console.log("Password valid.");
            this.validPasswd = 1;
        }
        else {
            console.log("Password invalid.");
            this.validPasswd = 0;
        }
    }


