import { Component, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Platform } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { Createacc4Page } from '../createacc4/createacc4';
import { HTTP } from '@ionic-native/http';
import { GlobalsProvider } from '../../providers/globals/globals';
import { ToastController } from 'ionic-angular';
import { Sim } from '@ionic-native/sim';
import 'rxjs/add/operator/toPromise';

declare var google;
declare var WifiInfo;
declare var GPSLocation;

@IonicPage()
@Component({
  selector: 'page-createacc3',
  templateUrl: 'createacc3.html',
})


//https://ionicthemes.com/tutorials/about/ionic-2-google-maps-google-places-geolocation
export class Createacc3Page {

  map: any;
  GoogleAutocomplete: any;
  autocomplete: any;
  autocompleteItems: any;
  currentPrediction: Array<any> = [];
  zone: NgZone = new NgZone({ enableLongStackTrace: true });

  address: string = '';
  pseudo: string = '';
  job: string = '';

  simInfo: any = {};

  sideAddress: any = {};
  location: any;
  gmLocation: {lat: number, lng: number} = {lat: 0, lng: 0};
  intPos: any = 0;
  intPosClick: any = 0;
  checklat: any = null;
  disconnectedToast: any = null;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private geolocation: Geolocation,
    private alertCtrl: AlertController,
    private platform: Platform,
    private http: HTTP,
    private globals: GlobalsProvider,
    private toastCtrl: ToastController,
    private sim: Sim
  ) {


    this.pseudo = navParams.get('pseudo');
    this.job = navParams.get('job');
    console.log(this.pseudo);
    console.log(this.job);


    this.watchPosition();

    this.GoogleAutocomplete = new google.maps.places.AutocompleteService();
    this.autocomplete = { input: '' };
    this.autocompleteItems = [];

    this.sim.hasReadPermission().then(
      (info) => {
        if (info == false) {
          this.sim.requestReadPermission().then(
            () => {
              console.log('Permission granted');
              this.sim.getSimInfo().then(
                (info) => {
                  console.log('Sim info: ');
                  console.log(JSON.stringify(info));
                  this.simInfo = info;
                },
                (err) => console.log('Unable to get sim info: ', err)
              );
            },
            () => console.log('Permission denied')
          );
        } else {
          this.sim.getSimInfo().then(
            (info) => {
              console.log('Sim info: ');
              console.log(JSON.stringify(info));
              this.simInfo = info;
            },
            (err) => console.log('Unable to get sim info: ', err)
          );
        }
        console.log('Has permission: ');
        console.log(JSON.stringify(info));
      },
      error => {
        console.log("Hasn't permission");
        console.log(JSON.stringify(error));
      }

    );

  }

  ionViewDidLoad() {

    this.onLocateUser();
    
    this.requestAutoComplete();

    console.log('ionViewDidLoad Createacc3Page');
  }

  public onLocateUser() {
    this.intPos = setInterval(() => {
      this.geolocation.getCurrentPosition()
      .then(
        (location) => {
          console.log('position gotten: long:',location.coords.longitude,' lat:',location.coords.latitude);
          this.checklat = location.coords.latitude;
          this.location = location;
          this.gmLocation.lat = location.coords.latitude;
          this.gmLocation.lng = location.coords.longitude;
          if(this.checklat !== null) {
          let pos = this.gmLocation.lat + ',' + this.gmLocation.lng;
          this.http.get('https://maps.googleapis.com/maps/api/place/autocomplete/json?input=a&key=' + this.globals.variables.API_KEY + '&location=' + pos + '&radius=100',
            {},
            {})
            .then(
              data => {
                console.log("**************Result from autocomplete**************");
                console.log(JSON.stringify(data.data));
                let predicts = JSON.parse(data.data).predictions;
                this.sideAddress = predicts[0];
              },
              error => {
                console.log("Error from autocomplete");
                console.log(JSON.stringify(error));
              }
            )
            clearInterval(this.intPos);
          }
        }
      )
      .catch(
        (error) => {
          console.log('Error getting location', error);
        }
      )
    },3000);
  }

  public getCurentPosition() {
    console.log("get Coords")
    this.geolocation.getCurrentPosition().then((resp) => {
      console.log("Coords")
      console.log(JSON.stringify(resp));
      // resp.coords.latitude
      // resp.coords.longitude
     }).catch((error) => {
       console.log('Error getting location', error);
     });
  }

  public watchPosition() {
    let watch = this.geolocation.watchPosition();
    watch.subscribe((data) => {
      console.log("Watching Position createaccount");
      console.log(JSON.stringify(data));
    });
  }

  public setSideAddress() {
    if (this.sideAddress.description != null) {
      this.address = JSON.stringify(this.sideAddress);
      this.autocomplete.input = this.sideAddress.description;
      this.autocompleteItems = [];
      clearInterval(this.intPosClick);
    } else {
      this.toastCtrl.create({
        message: 'En attente du GPS',
        duration: 5000,
        position: 'bottom'
      }).present();
      this.intPosClick = setInterval(() => {
        if (this.sideAddress.description != null) {
          this.address = JSON.stringify(this.sideAddress);
          this.autocomplete.input = this.sideAddress.description;
          this.autocompleteItems = [];
          clearInterval(this.intPosClick);
        } else {
          this.toastCtrl.create({
            message: 'En attente du GPS',
            duration: 5000,
            position: 'bottom'
          }).present();
        }
      },5000)
    }
  }

  public requestAutoComplete() {
    console.log("Check GPS");
    GPSLocation.getCurrentPosition((position) => {
      console.log("Resultat du GPS");
      console.log(JSON.stringify(position));
      let pos = parseFloat(parseFloat(position.coords.latitude).toFixed(6)) +
        ',' + parseFloat(parseFloat(position.coords.longitude).toFixed(6));
      //https://maps.googleapis.com/maps/api/place/autocomplete/output?parameters
      this.http.get('https://maps.googleapis.com/maps/api/place/autocomplete/json?input=a&key=' + this.globals.variables.API_KEY + '&location=' + pos + '&radius=100',
        {},
        {})
        .then(
          data => {
            let predicts = JSON.parse(data.data).predictions;
            this.sideAddress = predicts[0];
          },
          error => {
            console.log("Error from autocomplete");
            console.log(JSON.stringify(error));
          }
        )
    },
      (error) => {
        console.log("Erroeur du GPS");
        console.log(JSON.stringify(error));
        let alert = this.alertCtrl.create({
          title: 'Information',
          subTitle: 'Activer votre GPS, SVP.',
          buttons: ['Ok']
        });
        alert.present();
      });


  }

  public updateSearchResults() {
    console.log("Typing");
    clearInterval(this.intPosClick);
    if (this.autocomplete.input == '') {
      this.autocompleteItems = [];
      return;
    }
    this.GoogleAutocomplete.getPlacePredictions({ input: this.autocomplete.input },
      (predictions, status) => {
        if (predictions != null) {
          this.autocompleteItems = [];
          this.zone.run(() => {
            predictions.forEach((prediction) => {
              this.autocompleteItems.push(prediction);
            });
          });
        }

        console.log(this.autocompleteItems);
      });
  }

  public getCurentPrediction() {

    if (navigator.geolocation) {
      console.log("Navigator support Geolocation");
      navigator.geolocation.getCurrentPosition(function (position) {
        var pos = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
        console.log("Current lat and long");
        console.log(JSON.stringify(pos));
      },
        function (error) {
          console.log("Error");
          console.log(JSON.stringify(error));
        });

    }
    else {
      console.log("Navigator can not support Geolocation");
    }

  }

  public selectSearchResult(item) {
    console.log(JSON.stringify(item));
    this.address = JSON.stringify(item);
    this.autocomplete.input = item.description;
    this.autocompleteItems = [];
  }

  public precedent() {
    this.navCtrl.pop();
  }

  public nextStep() {
    clearInterval(this.intPosClick);
    if (this.address != null && this.address != '') {
      let caccn = Createacc4Page;
      this.navCtrl.push(caccn, {
        pseudo: this.pseudo,
        job: this.job,
        address: this.address
      });
    }
    else {
      let alert = this.alertCtrl.create({
        title: 'Information',
        subTitle: 'Indiquez votre adresse.',
        buttons: ['Ok']
      });
      alert.present();
    }

  }

}



