import { Component, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ModalController, Platform } from 'ionic-angular';
import { HTTP } from '@ionic-native/http';
import { GlobalsProvider } from '../../providers/globals/globals';
import { Toast } from '@ionic-native/toast';
import { FaqfilterPage } from '../faqfilter/faqfilter';

/**
 * Generated class for the FormationaddPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-formationadd',
  templateUrl: 'formationadd.html',
})
export class FormationaddPage {

  mail_object: string = 'Proposition de formation';
  mail_content: string = '';
  centre_de_formation: string = '';
  lien: string = '';
  phone: string = '';
  address: string = '';

  user: any = {};
  load: number = 0;

  selectedType: any = {};
  selectedDisple: any = {};

  listTypes: Array<any> = [];

  listDisciples: Array<any> = [];

  autocompleteItems: Array<any> = [];
  GoogleAutocomplete: any;
  zone: NgZone = new NgZone({ enableLongStackTrace: true });


  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private http: HTTP,
    private globals: GlobalsProvider,
    private alertCtrl: AlertController,
    private toast: Toast,
    private modalCtrl: ModalController,
    private platform: Platform
  ) {

    this.user = navParams.get('user');

    this.getListTypes();
    this.getListDisciples();

    platform.registerBackButtonAction(() => {
      navCtrl.pop();
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FormationaddPage');
  }

  public retour() {
    this.navCtrl.pop();
  }

  public selectSearchResult(item) {
    console.log(JSON.stringify(item));
    this.address = item.description;
  }

  public updateSearchResults() {
    console.log("Typing");
    if (this.address == '') {
      this.autocompleteItems = [];
      return;
    }
    this.GoogleAutocomplete.getPlacePredictions({ input: this.address },
      (predictions, status) => {
        if (predictions != null) {
          this.autocompleteItems = [];
          this.zone.run(() => {
            predictions.forEach((prediction) => {
              this.autocompleteItems.push(prediction);
            });
          });
        }

        console.log(this.autocompleteItems);
      });
  }

  public send() {
    this.load = 1;
    if (this.mail_content != '' && this.mail_object != '') {
      let formation = "<br>E-mail d'utilisateur : " + this.user.mail + "<br>" +
        "ID d'utilisateur : " + (this.user.id_User != null ? this.user.id_User : this.user.id) + "<br>" +
        "Objet du mail : " + this.mail_object + "<br>" +
        "Centre de la formation : " + this.centre_de_formation + "<br>" +
        "Type : " + this.selectedType.name + "<br>" +
        "Discipline : " + this.selectedDisple.name + "<br>" +
        "Contenu du mail : " + this.mail_content + "<br>" + 
        "Lien : " + this.lien + "<br>" + 
        "Téléphone : " + this.phone + "<br>";
      this.http.post(this.globals.variables.urls.reqAddForm,
        {
          "user_id": this.user.id_User != null ? this.user.id_User : this.user.id,
          "frm": formation,
          "centre": "" + this.centre_de_formation,
          "address": this.address
        },
        {})
        .then(data => {
          this.load = 0;
          let res = JSON.parse(data.data);
          if (res.status == 'demande-ok') {
            this.toast.show("Votre demande a été envoyée !", '3000', 'bottom')
              .subscribe(toast => {
                console.log(toast);
              });

          }
          else {
            this.toast.show("Une erreur c'est produit lors de l'envoi de votre demande !", '3000', 'center')
              .subscribe(toast => {
                console.log(toast);
              });
          }
          console.log(JSON.stringify(data.data));
          this.navCtrl.pop();
        },
          error => {
            this.load = 0;
            this.toast.show("Une erreur c'est produit lors de l'envoi de votre demande !", '3000', 'center')
              .subscribe(toast => {
                console.log(toast);
              });
            console.log(JSON.stringify(error));
            this.navCtrl.pop();
          });
    }
    else {
      this.load = 0;
      let alert = this.alertCtrl.create({
        title: 'Information',
        subTitle: 'Le contenu ne peut pas être vide',
        buttons: ['Ok']
      });
      alert.present();
    }

  }

  public ChooseType() {
    let filter = this.modalCtrl.create(FaqfilterPage,
      {
        user: this.user,
        theme: this.listTypes,
        title: "Types de formation",
        selectBefore: this.selectedType
      });
    filter.onDidDismiss(data => {
      if (data.id != '' && data.id != 'retour' && data.id != 'all') {
        this.selectedType = data;
        console.log("Selected theme :");
        console.log(JSON.stringify(this.selectedType));
      }
      else {
        //this.selectedTheme = {};
      }

    });
    filter.present();
  }

  public ChooseDisple() {
    let filter = this.modalCtrl.create(FaqfilterPage,
      {
        user: this.user,
        theme: this.listDisciples,
        title: "Catégories",
        selectBefore: this.selectedDisple
      });
    filter.onDidDismiss(data => {
      if (data.id != '' && data.id != 'retour' && data.id != 'all') {
        this.selectedDisple = data;
        console.log("Selected theme :");
        console.log(JSON.stringify(this.selectedDisple));
      }
      else {
        //this.selectedTheme = {};
      }

    });
    filter.present();
  }

  public getListTypes() {
    //document.getElementById('load').style.display = 'inline';
    this.http.post(this.globals.variables.urls.listTypesFormat, {}, {})
      .then(
        data => {
          this.listTypes = JSON.parse(data.data);
          this.load = 0;
        },
        error => {
          this.load = 0;
          console.log(JSON.stringify(error));
        }
      );
  }

  public getListDisciples() {
    //document.getElementById('load').style.display = 'inline';
    this.http.post(this.globals.variables.urls.listFormatiosCat, {}, {})
      .then(
        data => {
          this.listDisciples = JSON.parse(data.data);
          this.load = 0;
        },
        error => {
          this.load = 0;
          console.log(JSON.stringify(error));
        }
      );
  }

}



