  public getImagesGalerie(baseURL, dirToList) {

    this.file.listDir(baseURL, dirToList)
      .then((listing) => {
        let idImg = 0;
        listing.forEach(value => {
          //console.log(JSON.stringify(value));
          let fileExt = value.name.substring(value.name.lastIndexOf('.') + 1, value.name.length) || value.name;
          //console.log("Extension :::::::::::::::: " + fileExt);

          if (value.isFile && (fileExt == "png" || fileExt == "jpg" || fileExt == "jpeg" || fileExt == "gif")) {

            //console.log("Getting image data base64");

            this.file.readAsDataURL(baseURL + dirToList, value.name)
              .then(url => {
                let img = {
                  id: idImg,
                  url: url,
                  data: value
                };
                this.listImage.push(img);
                idImg++;
                if (this.selectedImg == null) this.selectedImg = this.listImage[0];
                //console.log(url);
              })
              .catch(error => {
                console.log(JSON.stringify(error));
              });

          }
          else if (value.isDirectory && value.name != 'cache') {
            this.getImagesGalerie(baseURL + dirToList + '/', value.name);
          }

        });

      })
      .catch(ex => {
        console.log('error');
        console.log(JSON.stringify(ex));
      });


  }

