  public addToFavorite(faq) {
    try {
      this.http.post(this.globals.variables.urls.addFavorite, {
        "user_id": (this.user.id_User != null ? this.user.id_User : this.user.id),
        "post_id": faq.faq.id
      },
        {})
        .then(
          data => {
            let res = JSON.parse(data.data);
            if (res.status == "ok") {
              if (faq.faq.user_favorite == 0) {
                faq.faq.favorite_count = parseInt(faq.faq.favorite_count) + 1;
                faq.faq.user_favorite = 1;
                this.toast.show("Vous avez aimé ce partage", "2000", "bottom").subscribe(
                  toast => {
                    console.log(toast);
                  }
                );
              }
              else {
                faq.faq.favorite_count = parseInt(faq.faq.favorite_count) - 1;
                faq.faq.user_favorite = 0;
                this.toast.show("Vous avez annulé votre \"J'aime\"", "2000", "bottom").subscribe(
                  toast => {
                    console.log(toast);
                  }
                );
              }

            }
            else {
              console.log(JSON.stringify(res));
              this.toast.show("Vous avez déjà aimé ce partage", "2000", "bottom").subscribe(
                toast => {
                  console.log(toast);
                }
              );
            }
            //console.log(JSON.stringify(data.data));
          },
          error => {
            console.log(JSON.stringify(error));
          }
        );
    }
    catch (error) {
      console.log("Error from addToFavorite")
      console.log(JSON.stringify(error))
    }
  }

