import { Component, ViewChild } from '@angular/core';
import { NavController, ViewController } from 'ionic-angular';
import { AngularCropperjsComponent } from 'angular-cropperjs';
import { CameraOptions, Camera } from '@ionic-native/camera';

/**
 * Generated class for the PhotopopupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-photopopup',
  templateUrl: 'photopopup.html',
})
export class PhotopopupPage {
  @ViewChild('angularCropper') public angularCropper: AngularCropperjsComponent;
  cropperOptions: any;
  croppedImage = null;


  constructor(
      public navCtrl: NavController,
      private viewCtrl: ViewController, 
      private camera: Camera
      ) {}

  base64UploadFromCamera() {
    const options: CameraOptions = {
      quality: 100,
      targetWidth: 750,
      targetHeight: 750,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      sourceType: this.camera.PictureSourceType.CAMERA,
      correctOrientation: true
    }

    this.camera.getPicture(options).then((imageData) => {
      if (imageData != null) {
        let img = {
          id: new Date().getTime(),
          url: 'data:image/jpeg;base64,' + imageData,
          data: { nativeURL: 'data:image/jpeg;base64,' + imageData },
          base64: 'data:image/jpeg;base64,' + imageData,
          source: "galery"
        };
        this.viewCtrl.dismiss(img);
      }
      else {
        this.viewCtrl.dismiss(null);
      }
    }, (err) => {
      console.log("************captureImage****************")
      console.log(err);
    });
  }

  openGallery() {
    const options: CameraOptions = {
      quality: 100,
      targetWidth: 800,
      targetHeight: 800,
      destinationType: this.camera.DestinationType.DATA_URL,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      correctOrientation: true
    }
      this.camera.getPicture(options).then((imageData) => {
        if (imageData != null) {
          let img = {
            id: new Date().getTime(),
            url: 'data:image/jpeg;base64,' + imageData,
            data: { nativeURL: 'data:image/jpeg;base64,' + imageData },
            base64: 'data:image/jpeg;base64,' + imageData,
            source: "galery"
          };
          this.viewCtrl.dismiss(img);
        }
        else {
          this.viewCtrl.dismiss(null);
        }
      }, (err) => {
        console.log("************captureGallery****************")
        console.log(err);
      });
  }

  fermerDismiss() {
    this.viewCtrl.dismiss(null);
  }
}


