  public showFilter() {

    let seleselectBefore = []
    if (this.category.id != null) {
      seleselectBefore.push(this.category)
    }
    else if (this.category.length > 0) {
      // seleselectBefore.concat(this.category)
      for (let gh = 0; gh < this.category.length; gh++) {
        seleselectBefore.push(this.category[gh])
      }
    }

    if (this.type.id != null ) {
      seleselectBefore.push(this.type)
    }
    else if (this.type.length > 0) {
      for (let gh = 0; gh < this.type.length; gh++) {
        seleselectBefore.push(this.type[gh])
      }
      
    }

    console.log("seleselectBefore")
    console.log(JSON.stringify(this.category))
    console.log(JSON.stringify(this.type))
    console.log(JSON.stringify(seleselectBefore))
    let filtre = this.modalCtrl.create(FaqfilterPage,
      {
        theme: this.listCat,
        listTyp: this.listType,
        user: this.user,
        title: "Filtres",
        selectBefore: seleselectBefore,
        selectCate: this.category,
        selectType: this.type,
        source: "form_filter"
      });

    filtre.onDidDismiss(
      (data) => {
        this.platform.registerBackButtonAction(() => {
          this.navCtrl.pop();
        });

        console.log("JSON.stringify(data)")
        console.log(JSON.stringify(data))

        this.category = data.selectedCat != null ? data.selectedCat : this.category;
        this.type = data.selectedTyp != null ? data.selectedTyp : this.type;

        if (this.category.id != null) {
          this.id_cat_f = this.category.id;
        }
        else if (this.category.length > 0) {
          for (let c = 0; c < this.category.length; c++) {
            this.id_cat_f += this.category[c].id + '-';
          }
        }
        else {
          this.id_cat_f = '';
        }

        if (this.type.id != null) {
          this.id_typ_f = this.type.id;
        }
        else if (this.type.length > 0) {
          for (let c = 0; c < this.type.length; c++) {
            this.id_typ_f += this.type[c].id + '-';
          }
        }
        else {
          this.id_typ_f = '';
        }
        this.load = 1;
        this.paging = 0;
        this.getListFormations(null, false);
      }
    )
    filtre.present();
  }

