import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Network } from '@ionic-native/network';
import { DentartPage } from '../dentart/dentart';

/**
 * Generated class for the NonetworkPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-nonetwork',
  templateUrl: 'nonetwork.html',
})
export class NonetworkPage {

  verified: number = 0;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private network: Network
  ) {

    //this.testConnectivity();

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad NonetworkPage');
  }

  public testConnectivity(){
    console.log("Testing network");
    if(this.network.type == 'none' || this.network.type == 'unknown'){
      /* this.rootPage = NonetworkPage;
      console.log("You are not connected"); */
    }
    else if (this.network.type != 'none' && this.network.type != 'unknown' && this.verified == 0){
      this.navCtrl.setRoot(DentartPage);
      this.verified = 1;
    }
    setTimeout(() => {
      this.testConnectivity();
    }, 5000);
  }

}



