import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, Platform } from 'ionic-angular';
import { HTTP } from '@ionic-native/http';
import { GlobalsProvider } from '../../providers/globals/globals';
import { Toast } from '@ionic-native/toast';

/**
 * Generated class for the MailtoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-mailto',
  templateUrl: 'mailto.html',
})
export class MailtoPage {

  user: any = {};
  ad: any = {};

  mail_object: string = '';
  ad_description: string = '';

  mail: string = "";
  post_id: string = "";
  object: string = "";
  content: string = "";

  isDisabled: boolean = true;
  load: number = 0;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private viewCtrl: ViewController,
    private http: HTTP,
    private globals: GlobalsProvider,
    private toast: Toast,
    private platform: Platform
  ) {

    this.user = navParams.get('user');
    this.ad = navParams.get('ad');
    this.mail = navParams.get('mail');
    this.post_id = navParams.get('post_id');
    this.object = navParams.get('object');

    platform.registerBackButtonAction(() => {
      viewCtrl.dismiss()
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MailtoPage');
  }

  public hideMailTo() {
    this.viewCtrl.dismiss();
  }

  public sendToMail() {
    this.load = 1;
    //contactAsMail
    this.http.post(this.globals.variables.urls.contactAsMail,
      {
        "post_id": "" + this.post_id,
        "mail": this.mail,
        "object": this.object,
        "content": this.content
      },
      {})
      .then(
        data => {
          this.load = 0;
          let res = JSON.parse(data.data);
          if (res.status == "sent") {
            this.toast.show('Votre mail a été envoyé', '3000', 'bottom').subscribe(
              toas => {
                console.log(toas);
              }
            );
          }
          else {
            this.toast.show('Mail non envoyé', '3000', 'bottom').subscribe(
              toas => {
                console.log(toas);
              }
            );
          }
          console.log("Resultat d'envoi mail");
          console.log(JSON.stringify(data));
          this.viewCtrl.dismiss();
        },
        error => {
          this.load = 0;
          this.toast.show('Erreur lors de l\'envoi du mail', '3000', 'bottom').subscribe(
            toas => {
              console.log(toas);
            }
          );
          console.log("Erreur d'envoi mail");
          console.log(JSON.stringify(error));
          this.viewCtrl.dismiss();
        }
      );
  }

}



