  public getListFaQs(infiniteScroll) {
    console.log("Getting list FaQs");
    this.http.get(this.globals.variables.urls.listFaQByUser + '?offset=' + this.paging,
      {
        'user_id': "" + (this.user.id_User != null ? this.user.id_User : this.user.id)
      },
      {})
      .then(
        data => {
          let res = JSON.parse(data.data);

          if (res.length <= 0) {
            this.load = 0;

            console.log('Async operation has ended');
            if (infiniteScroll != null)
              infiniteScroll.complete();
            setTimeout(() => {
              this.result_list = 0;
            }, 500)
            // setTimeout(() => {
            //   this.result_list = 1;
            // }, 1000)
          }
          else {
            for (let i = 0; i < res.length; i++) {

              if (res[i].comments) {
                let coms = [];
                for (let elem in res[i].comments) {
                  coms.push(res[i].comments[elem]);
                }

                let images_count = 0;
                let char70 = "";
                if (res[i].description.length > 70) {
                  char70 = "" + res[i].description.substr(0, 69) + "...";
                }
                else {
                  char70 = res[i].description;
                }


                if (res[i].faq_image_1 != '' && res[i].faq_image_1 != null) images_count += 1;
                if (res[i].faq_image_2 != '' && res[i].faq_image_2 != null) images_count += 1;
                if (res[i].faq_image_3 != '' && res[i].faq_image_3 != null) images_count += 1;
                if (res[i].faq_image_4 != '' && res[i].faq_image_4 != null) images_count += 1;



                res[i].images_count = images_count;
                res[i].comments_count = coms.length;
                res[i].coms = coms;
                res[i].char70 = char70;
                res[i].dateFR = res[i].date.substr(8, 2) + '/' + res[i].date.substr(5, 2) + '/' +
                  res[i].date.substr(0, 4) + ' ' + res[i].date.substr(11, 5);

              }

              for (let j = 0; j < this.listFaQTheme.length; j++) {
                if (this.listFaQTheme[j].id == res[i].theme) {

                  let newFaQ = {
                    faq: res[i],
                    theme: this.listFaQTheme[j],
                    type: 'faq'
                  }

                  this.getFaQAuthor(res[i].author_id, i, res.length, newFaQ, infiniteScroll, "faq");
                }

              }

            }
          }

        },
        error => {
          this.load = 0;
          console.log(JSON.stringify(error));
        }
      );
  }

